<template>
  <div class="popup-overlay" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="popup-content">
      <div class="header">
        <div class="semi-header">
          <SvgIcon name="bannerIcon"></SvgIcon>

          <h3 v-if="type === 'view'">{{ $t("OrderDetails") }}</h3>
          <h3 v-if="type === 'add'">{{ $t("AddOrder") }}</h3>
          <h3 v-if="type === 'edit'">{{ $t("EditOrder") }}</h3>
          <h3 v-if="type === 'delete'">{{ $t("DeleteOrder") }}</h3>
        </div>
        <button class="close-button" @click="$emit('close')">X</button>
      </div>
      <div class="form" v-if="type !== 'delete'">
        <div class="input-container">
          <label>{{ $t("OrderNumber") }}</label>
          <input v-model="formData.id" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("CustomerName") }}</label>
          <input v-model="formData.customerName" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("Phone") }}</label>
          <input v-model="formData.phone" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("orderDetails") }}</label>
          <input
            type="text"
            v-model="orderDetailInput"
            :disabled="type === 'view'"
            @keyup.enter="addOrderDetail"
            :placeholder="$t('AddorderpressEnter')"
          />
        </div>
        <ul>
          <li
            v-for="(detail, index) in formData.address_description"
            :key="index"
          >
            {{ detail }}
            <button v-if="type !== 'view'" @click="removeOrderDetail(index)">
              
            </button>
          </li>
        </ul>
        <div class="input-container">
          <label>{{ $t("TotalPrice") }}</label>
          <input v-model="formData.total_price" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("RestaurantName") }}</label>
          <input
            v-model="formData.restaurant_branch"
            :disabled="type === 'view'"
          />
        </div>
        <div class="input-container">
          <label>{{ $t("OrderStatus") }}</label>
          <select v-model="formData.status" :disabled="type === 'view'">
            <option v-for="status in statuses" :key="status" :value="status">
              {{ status }}
            </option>
          </select>
        </div>
        <div class="input-container">
          <label>{{ $t("OrderDate") }}</label>
          <input
            type="date"
            v-model="formData.created_at"
            :disabled="type === 'view'"
          />
        </div>
      </div>

      <p style="text-align: center; color: black" v-if="type === 'delete'">
        {{ $t("confirmDeleteOrder") }}
      </p>
      <p style="text-align: center" v-if="type === 'delete'">
        {{ $t("deleteWarning") }}
      </p>

      <div class="popup-actions">
        <button
          style="background-color: #f02116"
          v-if="type === 'delete'"
          @click="deleteOrder"
        >
          {{ $t("delete") }}
        </button>
        <button v-if="type === 'add' || type === 'edit'" @click="saveOrder">
          {{ $t("Save") }}
        </button>
        <button @click="$emit('close')">{{ $t("Cancel") }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  components: {
    SvgIcon,
  },
  props: {
    type: String,
    data: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      formData: {
        ...this.data,
        address_description: Array.isArray(this.data.address_description)
          ? this.data.address_description
          : this.data.address_description
          ? [this.data.address_description] // Convert to array if it's a string
          : [], // Default to an empty array
        status: this.data.status || "new", // Ensure status has a default value
      },
      orderDetailInput: "",
      statuses: ["new", "waiting", "completed", "processing"], // Ensure statuses are defined
      toast: useToast(),
    };
  },
  watch: {
    data: {
      immediate: true,
      handler(newData) {
        this.formData = {
          ...newData,
          address_description: Array.isArray(newData.address_description)
            ? newData.address_description
            : newData.address_description
            ? [newData.address_description] // Convert to array if it's a string
            : [], // Default to an empty array
          status: newData.status || "", // Ensure status has a default value
        };
      },
    },
  },
  methods: {
    addOrderDetail() {
      if (this.orderDetailInput.trim()) {
        this.formData.address_description.push(this.orderDetailInput.trim());
        this.orderDetailInput = "";
      }
    },
    removeOrderDetail(index) {
      this.formData.address_description.splice(index, 1);
    },
    validateForm() {
      if (!this.formData.id || this.formData.id.toString().trim() === "") {
        this.toast.error(this.$t("OrderNumber") + " " + this.$t("isRequired"));
        return false;
      }
      if (
        !this.formData.customerName ||
        this.formData.customerName.trim() === ""
      ) {
        this.toast.error(this.$t("CustomerName") + " " + this.$t("isRequired"));
        return false;
      }
      if (!this.formData.phone || this.formData.phone.trim() === "") {
        this.toast.error(this.$t("Phone") + " " + this.$t("isRequired"));
        return false;
      }
      if (
        !this.formData.address_description ||
        this.formData.address_description.length === 0
      ) {
        this.toast.error(this.$t("orderDetails") + " " + this.$t("isRequired"));
        return false;
      }
      if (
        !this.formData.total_price ||
        this.formData.total_price.toString().trim() === ""
      ) {
        this.toast.error(this.$t("TotalPrice") + " " + this.$t("isRequired"));
        return false;
      }
      if (
        !this.formData.restaurant_branch ||
        this.formData.restaurant_branch.trim() === ""
      ) {
        this.toast.error(
          this.$t("RestaurantName") + " " + this.$t("isRequired")
        );
        return false;
      }
      if (!this.formData.status || this.formData.status.trim() === "") {
        this.toast.error(this.$t("OrderStatus") + " " + this.$t("isRequired"));
        return false;
      }
      if (!this.formData.created_at || this.formData.created_at.trim() === "") {
        this.toast.error(this.$t("OrderDate") + " " + this.$t("isRequired"));
        return false;
      }
      return true;
    },
    saveOrder() {
      if (this.validateForm()) {
        this.$emit("save", this.formData);
        this.toast.success(this.$t("OrderSavedSuccessfully"));
      }
    },
    deleteOrder() {
      this.$emit("delete", this.formData.id);
      this.toast.success(this.$t("OrderDeletedSuccessfully"));
    },
  },
};
</script>

<style scoped>
.popup-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: #ffffff;
  border-radius: 16px;
  width: 610px;
  max-width: 90%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-button {
  background: none;
  font-family: Almarai !important;
  border: none;
  color: #292d32;
  font-size: 25px;
  margin: 0 20px 0 20px;
  cursor: pointer;
  transition: color 0.3s;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 12px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 559px;
  height: 58px;
  padding: 8px 16px;
  border: none;

  font-family: Almarai !important;
  border-radius: 40px;
  font-size: 14px;
  cursor: pointer;
}

.popup-actions button:first-child {
  background-color: var(--primary-color);
  font-family: Almarai !important;
  color: #ffffff;
}
@media (max-width: 768px) {
  .popup-actions button {
    width: 100%;
    height: auto;
    min-height: 48px;
    padding: 12px 16px;
  }

  .popup-actions {
    width: 100%;
  }
}

.popup-actions button:last-child {
  background-color: #ffffff;
    border: 1px solid #111111;

  color: #545454;
  font-family: Almarai !important;
}

ul {
  list-style: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

li {
  background-color: var(--primary-color);
  height: 35px;
  border-radius: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px 0 15px;
  color: #ffffff;
}

li button {
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  font-family: Almarai !important;
  color: var(--primary-color);
  background-color: #ffffff;
  padding: 2px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 6px;
  transition: color 0.3s;
  margin: 5px;
}
.form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
select {
  width: 99%;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
}
</style>
