<template>
  <div class="popup-overlay" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="popup-content">
      <div class="header">
        <div class="semi-header">
          <SvgIcon name="bannerIcon"></SvgIcon>

          <h3 v-if="type === 'view'">{{ $t("ViewNotification") }}</h3>
          <h3 v-else-if="type === 'edit'">{{ $t("EditNotification") }}</h3>
          <h3 v-else-if="type === 'add'">{{ $t("AddNotification") }}</h3>
          <h3 v-else>{{ $t("DeleteNotification") }}</h3>
        </div>
        <button class="close-button" @click="$emit('close')"></button>
      </div>
      <div class="form" v-if="type !== 'delete'">
        <div class="input-container">
          <label>{{ $t("NotificationName") }}</label>
          <input v-model="localData.name" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("Description") }}</label>
          <textarea
            v-model="localData.description"
            :disabled="type === 'view'"
          ></textarea>
        </div>

        <div v-if="type === 'view'" class="input-container file">
          <img
            :src="localData.photo"
            alt="Notification Image"
            class="preview-image"
          />
        </div>
        <div v-else class="input-container file">
          <label>{{ $t("AttachPicture_noti") }}</label>

          <div
            class="upload-area"
            @dragover.prevent="handleDragOver"
            @drop.prevent="handleDrop"
          >
            <SvgIcon name="uploadImage"></SvgIcon>
            <p>{{ $t("Drag_drop") }}</p>
            <p>{{ $t("Or") }}</p>
            <button @click="triggerFileInput">{{ $t("BrowseFiles") }}</button>
          </div>
          <input
            type="file"
            style="display: none"
            ref="fileInput"
            @change="handleImageUpload"
          />
        </div>
        <div class="input-container">
          <label>{{ $t("TheArea") }}</label>
          <input v-model="localData.TheArea" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("SendTo") }}</label>
          <select v-model="localData.sendTo" :disabled="type === 'view'">
            <option value="sellers">{{ $t("Sellers") }}</option>
            <option value="customers">{{ $t("Customers") }}</option>
          </select>
        </div>
      </div>

      <p style="text-align: center" v-else>{{ $t("ConfirmDelete") }}</p>
      <p v-if="type === 'delete'" style="text-align: center; color: #545454">
        {{ $t("deleteWarning") }}
      </p>

      <div class="popup-actions">
        <button v-if="type !== 'view'" @click="save">
          {{ type === "delete" ? $t("delete") : $t("Save") }}
        </button>
        <button @click="$emit('close')">{{ $t("Cancel") }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  setup() {
    // Get toast interface
    const toast = useToast();
    return { toast };
  },
  components: {
    SvgIcon,
  },
  props: {
    type: String,
    data: Object,
  },
  data() {
    return {
      localData: { ...this.data },
    };
  },
  methods: {
    handleImageUpload(event) {
      const file = event.target.files[0];
      this.uploadFile(file);
    },
    handleDragOver() {
      // Add visual feedback here, like changing the background color
    },
    handleDrop(event) {
      const file = event.dataTransfer.files[0];
      this.uploadFile(file);
    },
    uploadFile(file) {
      if (file) {
        // Image validation
        const validTypes = [
          "image/jpeg",
          "image/png",
          "image/gif",
          "image/webp",
        ];
        const maxSizeMB = 2; // max size 2MB
        if (!file.type) {
          this.toast.error(
            "File type is not recognized. Please upload a valid image."
          );
          return;
        }
        const fileType = file.type.toLowerCase();
        if (!validTypes.includes(fileType)) {
          return;
        }
        if (file.size > maxSizeMB * 1024 * 1024) {
          this.toast.error(`File size exceeds ${maxSizeMB}MB limit.`);
          return;
        }
        const reader = new FileReader();
        reader.onload = (e) => {
          this.localData.photo = e.target.result;
          this.toast.success("Image uploaded successfully!");
        };
        reader.onerror = () => {
          this.toast.error("Error reading the file. Please try again.");
        };
        reader.readAsDataURL(file);
      }
    },
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    save() {
      // Validation logic
      if (
        !this.localData.name ||
        !this.localData.description ||
        !this.localData.TheArea ||
        !this.localData.sendTo
      ) {
        this.toast.error("Please fill in all required fields."); // Show error toast
        return;
      }

      if (
        (this.type === "add" || this.type === "edit") &&
        !this.localData.photo
      ) {
        this.toast.error("Please upload an image."); // Show error toast if no image on add or edit
        return;
      }

      if (this.type === "delete") {
        this.$emit("save", this.data);
        this.toast.success("Notification deleted successfully!"); // Show success toast on delete
      } else {
        this.$emit("save", this.localData);
        this.toast.success("Notification saved successfully!"); // Show success toast
      }
    },
  },
};
</script>

<style scoped>
.popup-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: #ffffff;
  border-radius: 16px;
  width: 610px;
  max-width: 90%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-button {
  background: none;
  border: none;
  color: #292d32;
  font-size: 25px;
  margin: 0 20px 0 20px;
  cursor: pointer;
  font-family: Almarai !important;
  transition: color 0.3s;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 12px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 100%;
  max-width: 559px;
  height: 58px;
  padding: 8px 16px;
  border-radius: 40px;
  font-size: 14px;
  cursor: pointer;
  border: none;
}

@media (max-width: 768px) {
  .popup-actions button {
    width: 100%;
    height: auto;
    min-height: 48px;
    padding: 12px 16px;
  }

  .popup-actions {
    width: 100%;
  }
}

.popup-actions button:first-child {
  background-color: var(--primary-color);
  color: #ffffff;
  font-family: Almarai !important;
}

.popup-actions button:last-child {
  background-color: #ffffff;
  border: 1px solid #111111;

  color: #545454;
  font-family: Almarai !important;
}

ul {
  list-style: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

li {
  background-color: var(--primary-color);
  width: 116px;
  height: 35px;
  border-radius: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px 0 15px;
  color: #ffffff;
}

li button {
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  color: var(--primary-color);
  background-color: #ffffff;
  padding: 2px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 6px;
  transition: color 0.3s;
}
.form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
select {
  width: 99%;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
.file {
  display: flex;
  justify-content: start;
  align-items: center;
  height: auto;
  flex-direction: column; /* Added */
}

.input-container .ph {
  height: 100px;
  width: 45%;
}
.input-container img {
  width: 150px;
  height: auto;
}
.preview-image {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
}

/* Styles for the upload area */
.upload-area {
  border: 2px solid #1b1f2714;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  width: 95%;
  cursor: pointer;
  margin-bottom: 10px;
}

.upload-area p {
  margin: 5px 0;
}

.upload-area button {
  background-color: var(--primary-color);
  color: #ffffff;
  border: none;
  padding: 8px 16px;
  border-radius: 40px;
  font-size: 14px;
  cursor: pointer;
}
</style>
