<template>
  <div class="popup-overlay" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="popup-content">
      <div class="header">
        <div class="semi-header">
          <SvgIcon name="bannerIcon"></SvgIcon>
          <h3 v-if="mode === 'add'">{{ $t("AddCustomer") }}</h3>
          <h3 v-if="mode === 'edit'">{{ $t("EditCustomer") }}</h3>
          <h3 v-if="mode === 'view'">{{ $t("ViewCustomer") }}</h3>
          <h3 v-if="mode === 'delete'">{{ $t("DeleteCustomer") }}</h3>
        </div>
        <button class="close-button" @click="$emit('close')">X</button>
      </div>
      <div class="form" v-if="mode !== 'delete'">
        <div class="input-container">
          <label>{{ $t("CustomerName") }}</label>
          <input
            v-model="localCustomer.customerName"
            :disabled="mode === 'view'"
            :class="{ 'error-input': errors.customerName }"
          />
          <span class="error-message" v-if="errors.customerName">{{
            errors.customerName
          }}</span>
        </div>
        <div class="input-container">
          <label>{{ $t("Phone") }}</label>
          <input
            v-model="localCustomer.phone"
            :disabled="mode === 'view'"
            :class="{ 'error-input': errors.phone }"
          />
          <span class="error-message" v-if="errors.phone">{{
            errors.phone
          }}</span>
        </div>
        <div class="input-container">
          <label>{{ $t("email") }}</label>
          <input
            v-model="localCustomer.email"
            :disabled="mode === 'view'"
            :class="{ 'error-input': errors.email }"
          />
          <span class="error-message" v-if="errors.email">{{
            errors.email
          }}</span>
        </div>
        <div class="input-container">
          <label>{{ $t("OrderCount") }}</label>
          <input
            v-model="localCustomer.ordersCount"
            type="number"
            :disabled="mode === 'view'"
            :class="{ 'error-input': errors.ordersCount }"
          />
          <span class="error-message" v-if="errors.ordersCount">{{
            errors.ordersCount
          }}</span>
        </div>
        <div class="input-container">
          <label>{{ $t("TotalSpending") }}</label>
          <input
            v-model="localCustomer.ordersTotal"
            type="number"
            :disabled="mode === 'view'"
            :class="{ 'error-input': errors.ordersTotal }"
          />
          <span class="error-message" v-if="errors.ordersTotal">{{
            errors.ordersTotal
          }}</span>
        </div>
        <div class="input-container">
          <label>{{ $t("LastOrderDate") }}</label>
          <input
            v-model="localCustomer.LastOrderDate"
            type="date"
            :disabled="mode === 'view'"
          />
          <span class="error-message" v-if="errors.LastOrderDate">{{
            errors.LastOrderDate
          }}</span>
        </div>
      </div>
      <p style="text-align: center" v-if="mode === 'delete'">
        {{ $t("confirmDeleteCustomer") }}
      </p>
      <p v-if="mode === 'delete'" style="text-align: center; color: #545454">
        {{ $t("deleteWarning") }}
      </p>
      <div class="popup-actions">
        <button v-if="mode === 'add'" @click="save">{{ $t("Save") }}</button>
        <button v-if="mode === 'edit'" @click="save">{{ $t("Save") }}</button>
        <button
          style="background-color: #f02116; color: #ffffff"
          v-if="mode === 'delete'"
          @click="confirmDelete"
        >
          {{ $t("delete") }}
        </button>
        <button @click="$emit('close')">{{ $t("Cancel") }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  name: "customerPopup",
  components: {
    SvgIcon,
  },
  props: {
    mode: { type: String, required: true },
    customer: {
      type: Object,
      default: () => ({
        id: null,
        customerName: "",
        phone: "",
        email: "",
        ordersCount: 0,
        ordersTotal: 0,
        LastOrderDate: new Date().toISOString().split("T")[0],
      }),
    },
  },
  data() {
    return {
      localCustomer: {
        ...this.customer,
      },
      errors: {
        customerName: null,
        phone: null,
        email: null,
        ordersCount: null,
        ordersTotal: null,
        LastOrderDate: null,
      },
      toast: null,
    };
  },
  created() {
    this.toast = useToast();
  },
  watch: {
    customer: {
      immediate: true,
      handler(newCustomer) {
        this.localCustomer = { ...newCustomer }; // Update localCustomer when customer prop changes
      },
    },
  },
  methods: {
    validateForm() {
      let isValid = true;

      // Reset errors
      this.errors = {
        customerName: null,
        phone: null,
        email: null,
        ordersCount: null,
        ordersTotal: null,
        LastOrderDate: null,
      };

      // Validate required fields
      if (!this.localCustomer.customerName.trim()) {
        this.errors.customerName = this.$t("validation.required");
        if (this.toast) {
          this.toast.error(
            this.$t("validation.required") + ": " + this.$t("CustomerName")
          );
        }
        isValid = false;
      }

      if (!this.localCustomer.phone.trim()) {
        this.errors.phone = this.$t("validation.required");
        if (this.toast) {
          this.toast.error(
            this.$t("validation.required") + ": " + this.$t("Phone")
          );
        }
        isValid = false;
      }

      // Validate email required
      if (!this.localCustomer.email || !this.localCustomer.email.trim()) {
        this.errors.email = this.$t("validation.required");
        if (this.toast) {
          this.toast.error(
            this.$t("validation.required") + ": " + this.$t("email")
          );
        }
        isValid = false;
      } else {
        // Validate email format
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(this.localCustomer.email)) {
          this.errors.email = this.$t("validation.invalidEmail");
          if (this.toast) {
            this.toast.error(this.$t("validation.invalidEmail"));
          }
          isValid = false;
        }
      }

      // Validate date format (LastOrderDate)
      if (
        !this.localCustomer.LastOrderDate ||
        this.localCustomer.LastOrderDate.trim() === ""
      ) {
        this.errors.LastOrderDate = this.$t("validation.invalidDate");
        if (this.toast) {
          this.toast.error(this.$t("validation.invalidDate"));
        }
        isValid = false;
      } else {
        const date = new Date(this.localCustomer.LastOrderDate);
        if (isNaN(date.getTime())) {
          this.errors.LastOrderDate = this.$t("validation.invalidDate");
          if (this.toast) {
            this.toast.error(this.$t("validation.invalidDate"));
          }
          isValid = false;
        } else {
          this.errors.LastOrderDate = null;
        }
      }

      // Validate numeric fields
      if (
        this.localCustomer.ordersCount === "" ||
        isNaN(Number(this.localCustomer.ordersCount))
      ) {
        this.errors.ordersCount = this.$t("validation.numeric");
        if (this.toast) {
          this.toast.error(
            this.$t("validation.numeric") + ": " + this.$t("OrderCount")
          );
        }
        isValid = false;
      }

      if (
        this.localCustomer.ordersTotal === "" ||
        isNaN(Number(this.localCustomer.ordersTotal))
      ) {
        this.errors.ordersTotal = this.$t("validation.numeric");
        if (this.toast) {
          this.toast.error(
            this.$t("validation.numeric") + ": " + this.$t("TotalSpending")
          );
        }
        isValid = false;
      }

      return isValid;
    },
    save() {
      if (!this.validateForm()) {
        this.$emit("error", this.errors);
        if (this.toast) {
          this.toast.error(this.$t("validation.formErrors"));
        }
        return;
      }
      this.$emit("save", { ...this.localCustomer });
      if (this.toast) {
        this.toast.success(this.$t("validation.saveSuccess"));
      }
    },
    confirmDelete() {
      this.$emit("delete", this.localCustomer.id);
      this.toast.success(this.$t("validation.deletedSuccess"));
    },
  },
};
</script>

<style scoped>
.popup-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: #ffffff;
  border-radius: 16px;
  width: 610px;
  max-width: 90%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-button {
  background: none;
  border: none;
  color: #292d32;
  font-size: 25px;
  margin: 0 20px 0 20px;
  cursor: pointer;
  transition: color 0.3s;
  font-family: Almarai !important;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 12px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 100%;
  max-width: 559px;
  height: 58px;
  border: none;
  padding: 8px 16px;
  border-radius: 40px;
  font-size: 14px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .popup-actions button {
    height: 48px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .popup-actions button {
    height: 42px;
    padding: 6px 12px;
    font-size: 12px;
  }
}

.popup-actions button:first-child {
  background-color: var(--primary-color);
  color: #ffffff;
  font-family: Almarai !important;
}

.popup-actions button:last-child {
  background-color: #ffffff;
    border: 1px solid #111111;

  font-family: Almarai !important;
  color: #545454;
}

ul {
  list-style: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

li {
  background-color: var(--primary-color);
  width: 116px;
  height: 35px;
  border-radius: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px 0 15px;
  color: #ffffff;
}

li button {
  font-family: Almarai !important;
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  color: var(--primary-color);
  background-color: #ffffff;
  padding: 2px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 6px;
  transition: color 0.3s;
}
.form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
.input-container {
  position: relative;
  margin: 10px 0 20px 0;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none;
}
.error-message {
  position: absolute;
  bottom: -18px;
  left: 0;
  color: #f02116;
  font-size: 12px;
}
.error-input {
  border-color: #f02116 !important;
}
</style>
