<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="container">
        <div class="form-group">
          <label for="content">{{ $t("ContentManagement") }}</label>
          <select name="" id="">
            <option value=""> </option>
          </select>
        </div>
      </div>
      <div class="container">
        <div class="form-group">
          <label for="content"> {{ $t("Address1") }} </label>
          <div class="text-area" contenteditable="true" @input="updateAddress1">
            {{ Address1 }}
          </div>
        </div>
        <div class="form-group">
          <label for="content"> {{ $t("Description") }} </label>
          <div
            class="text-area"
            contenteditable="true"
            @input="updateDescription1"
          >
            {{ Description1 }}
          </div>
        </div>
        <div class="form-group">
          <label for="content"> {{ $t("Address2") }} </label>
          <div class="text-area" contenteditable="true" @input="updateAddress2">
            {{ Address2 }}
          </div>
        </div>
        <div class="form-group">
          <label for="content"> {{ $t("Description") }} </label>
          <div
            class="text-area"
            contenteditable="true"
            @input="updateDescription2"
          >
            {{ Description2 }}
          </div>
        </div>
        <div class="buttons">
          <button class="save-button" @click="saveData">{{ $t("add") }}</button>
          <button class="reset-button" @click="resetFields">
            {{ $t("Reset") }}
          </button>
        </div>
      </div>
      <div class="container">
        <div class="form-group">
          <label for="newQuestion">{{ $t("TheQuestion") }}</label>
          <div
            class="text-area"
            contenteditable="true"
            @input="updateNewQuestion"
          >
            {{ $t(newQuestion) }}
          </div>
        </div>
        <div class="form-group">
          <label for="newAnswer">{{ $t("TheAnswer") }}</label>
          <div
            class="text-area"
            contenteditable="true"
            @input="updateNewAnswer"
          >
            {{ $t(newAnswer) }}
          </div>
        </div>

        <div class="buttons">
          <button class="save-button" @click="saveData">{{ $t("add") }}</button>
          <button class="reset-button" @click="resetFields">
            {{ $t("Reset") }}
          </button>
        </div>
      </div>
      <helpComponent
        :questions="questions"
        @edit-question="handleEditQuestion"
        @delete-question="handleDeleteQuestion"
      />
    </div>
  </div>
</template>

<script>
import helpComponent from "@/components/helpComponent.vue";
import { useToast } from "vue-toastification";

export default {
  name: "AboutUs",
  components: {
    helpComponent,
  },
  data() {
    return {
      questions: [
        {
          id: 1,
          question: "premiumPlan?",
          answer: "premiumPlanContent",
        },
        {
          id: 2,
          question: "policyPrivacy?",
          answer: "policyPrivacyContent",
        },
        {
          id: 3,
          question: "contentManagement?",
          answer: "contentManagementContent",
        },
      ],

      description:
        "         .              .",
      content:
        "         .               ...... I",
      AboutUs: " ",
      Address1: "",
      Description1: "",
      Address2: "",
      Description2: "",
      newQuestion: "",
      newAnswer: "",
      editingId: null,
    };
  },
  setup() {
    const toast = useToast();
    return { toast };
  },
  computed: {},
  mounted() {},
  methods: {
    updateDescription1(event) {
      this.Description1 = event.target.textContent;
    },
    updateAddress1(event) {
      this.Address1 = event.target.textContent;
    },
    updateAddress2(event) {
      this.Address2 = event.target.textContent;
    },
    updateDescription2(event) {
      this.Description2 = event.target.textContent;
    },
    updateNewQuestion(event) {
      this.newQuestion = event.target.textContent;
    },
    updateNewAnswer(event) {
      this.newAnswer = event.target.textContent;
    },
    updateContent(event) {
      this.content = event.target.textContent;
    },
    updateContent1(event) {
      this.AboutUs = event.target.textContent;
    },
    saveData() {
      if (this.newQuestion.trim() && this.newAnswer.trim()) {
        if (this.editingId !== null) {
          const index = this.questions.findIndex(
            (q) => q.id === this.editingId
          );
          if (index !== -1) {
            this.questions[index].question = this.newQuestion;
            this.questions[index].answer = this.newAnswer;
            this.toast.success(this.$t("Question updated successfully"));
          }
          this.editingId = null;
        } else {
          const newId = this.questions.length
            ? Math.max(...this.questions.map((q) => q.id)) + 1
            : 1;
          this.questions.push({
            id: newId,
            question: this.newQuestion,
            answer: this.newAnswer,
          });
          this.toast.success(this.$t("Question added successfully"));
        }
        this.resetFields();
      } else {
        this.toast.error(
          this.$t("Please fill in both question and answer fields.")
        );
      }
    },
    resetFields() {
      this.newQuestion = "";
      this.newAnswer = "";
      // this.description = "";
      this.Address1 = "";
      this.Description1 = "";
      this.Address2 = "";
      this.Description2 = "";
      this.editingId = null;
    },
    handleEditQuestion(id) {
      const question = this.questions.find((q) => q.id === id);
      if (question) {
        this.newQuestion = question.question;
        this.newAnswer = question.answer;
        this.editingId = id;
      }
    },
    handleDeleteQuestion(id) {
      this.questions = this.questions.filter((q) => q.id !== id);
      this.toast.success(this.$t("Question deleted successfully"));
    },
  },
};
</script>
<style lang="scss" scoped>
.Control_panel {
  @media (min-width: 1199px) {
    margin-left: 20rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .Control_panel {
    margin-left: 20rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }

  .boxDetails {
    gap: 10px;
  }
}

@media (max-width: 768px) and (min-width: 991px) {
  .Control_panel {
    margin: 0 !important;
  }
}
/* Media Queries for Small Screens (below 768px) */
@media (max-width: 768px) {
  .Control_panel {
    margin: 0;
    padding: 0 10px;
  }
}

.container {
  border-radius: 30px;
  background-color: #ffffff;
  padding: 20px;
  margin: 20px;
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

.text-area {
  padding: 15px 30px;
  border-radius: 32px 32px 0 0;
  border: 1px solid #efefef;
  background-color: white;
  color: #555;
  min-height: 50px;
}
select {
  padding: 15px 30px;
  width: -webkit-fill-available;
  border-radius: 32px;
  border: 1px solid #efefef;
  background-color: white;
  color: #555;
}
.editor-toolbar {
  border: 1px solid #efefef;
  border-radius: 0 0 32px 32px;
  padding: 10px;
}

.editor-toolbar span {
  padding: 5px 10px;
  margin-right: 5px;
  margin-left: 5px;
  color: #6b6b6b;
  cursor: pointer;
}

.buttons {
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.save-button,
.reset-button {
  padding: 8px 50px;
  border: none;
  border-radius: 40px;
  cursor: pointer;
}

.save-button {
  background-color: var(--primary-color);
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
}

.reset-button {
  background-color: transparent;
  color: #000000;
  border: 1px solid #ddd;
  font-size: 16px;
  font-weight: 400;
}
</style>
