import { createRouter, createWebHistory } from "vue-router";
import dashboardHome from "../views/dachboard/dashboardHome.vue";
import DashboardLayout from "../layout/DashboardLayout.vue";
import OrderManagement from "../views/dachboard/OrderManagement.vue";
import ProductManagement from "../views/dachboard/ProductManagement.vue";
import CustomerManagement from "../views/dachboard/CustomerManagement.vue";
import RestaurantManagement from "../views/dachboard/RestaurantManagement.vue";
import ReportsAndAnalytics from "../views/dachboard/ReportsAndAnalytics.vue";
import SendNotifications from "../views/dachboard/SendNotifications.vue";
import AddBanners from "../views/dachboard/AddBanners.vue";
import settings from "../views/dachboard/settings.vue";
import Help_Center from "../views/dachboard/Help_Center.vue";
import privacy_policy from "../views/dachboard/privacy_policy.vue";
import About_Us from "../views/dachboard/About_Us.vue";
import ratings from "../views/dachboard/Ratings.vue";
import login from "../views/Login.vue";
import profile from "../views/profile.vue";
import forgetPassword from "../views/forgetPassword.vue";
import editProfile from "../views/editProfile.vue";

const routes = [

  {
    path: "/dashboard",
    name: "DashboardLayout",
    component: DashboardLayout,
    children: [
      {
        path: "/",
        name: "home",
        component: dashboardHome,
      },
      {
        path: "OrderManagement",
        name: "OrderManagement",
        component: OrderManagement,
      },
      {
        path: "ProductManagement",
        name: "ProductManagement",
        component: ProductManagement,
      },
      {
        path: "CustomerManagement",
        name: "CustomerManagement",
        component: CustomerManagement,
      },
      {
        path: "RestaurantManagement",
        name: "RestaurantManagement",
        component: RestaurantManagement,
      },
      {
        path: "ReportsAndAnalytics",
        name: "ReportsAndAnalytics",
        component: ReportsAndAnalytics,
      },
      {
        path: "ratings",
        name: "ratings",
        component: ratings,
      },
      {
        path: "AddBanners",
        name: "AddBanners",
        component: AddBanners,
      },
      {
        path: "SendNotifications",
        name: "SendNotifications",
        component: SendNotifications,
      },
      {
        path: "settings",
        name: "settings",
        component: settings,
      },
      {
        path: "privacy_policy",
        name: "privacy_policy",
        component: privacy_policy,
      },
      {
        path: "Help_Center",
        name: "Help_Center",
        component: Help_Center,
      },
      {
        path: "About_Us",
        name: "About_Us",
        component: About_Us,
      },
      {
        path: "profile",
        name: "profile",
        component: profile,
      },
      {
        path: "editProfile",
        name: "editProfile",
        component: editProfile,
      },
      {
        path: "forgetPassword",
        name: "forgetPassword",
        component: forgetPassword,
      },
    ]
  },
  
  {
    path: "/login",
    name: "login",
    component: login,
  },

];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
