<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="table-container">
        <div class="headerTable">
          <div class="icon_text">
            <div class="icon">
              <SvgIcon name="resturant"></SvgIcon>
            </div>
            <p>{{ $t("AllNotifications") }}</p>
          </div>
          <div class="search_add">
            <div class="search">
              <input type="text" v-model="filter" :placeholder="$t('search')" />
              <SvgIcon class="svg" name="search"></SvgIcon>
            </div>
            <button class="search-button" @click="openPopup('add', {})">
              <p class="addRestbtn">{{ $t("AddNotifi") }}</p>
              <span class="icon">
                <SvgIcon name="add"></SvgIcon>
              </span>
            </button>
          </div>
        </div>
        <div class="table-responsive">
          <table>
            <thead>
              <tr class="table-header">
                <th>
                  <div
                    style="
                      display: flex;
                      align-items: center;
                      margin-right: 10px;
                      gap: 5px;
                    "
                  >
                    <SvgIcon name="circle"></SvgIcon>

                    {{ $t("headTable1") }}
                  </div>
                </th>
                <th>{{ $t("head9Table2") }}</th>
                <th>{{ $t("head9Table3") }}</th>
                <th>{{ $t("head9Table4") }}</th>
                <th>{{ $t("head9Table5") }}</th>
                <th>{{ $t("head9Table6") }}</th>
                <th>{{ $t("head4Table7") }}</th>
                <th>{{ $t("AllProcedures") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(rows, index) in filteredRows"
                :key="`product-${index}`"
              >
                <td
                  style="
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                    gap: 5px;
                  "
                >
                  <SvgIcon name="dot_circle"></SvgIcon>
                  {{ rows.code }}
                </td>
                <td>{{ rows.photo }}</td>
                <td>{{ rows.name }}</td>
                <td>{{ rows.description }}</td>
                <td>{{ rows.TheArea }}</td>
                <td>
                  {{
                    rows.sendTo === "sellers" ? $t("Sellers") : $t("Customers")
                  }}
                </td>
                <td>
                  <label class="switch">
                    <input
                      type="checkbox"
                      v-model="rows.status"
                      @change="updateStatus(item)"
                    />
                    <span class="slider round"></span>
                  </label>
                </td>
                <td class="actions">
                  <div class="iconsIctions">
                    <div class="icon" @click="openPopup('edit', rows)">
                      <SvgIcon class="svg" name="edit"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('view', rows)">
                      <SvgIcon class="svg" name="view"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('delete', rows)">
                      <SvgIcon class="svg delete" name="deleteItem"></SvgIcon>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <notification-popup
    v-if="popupData.visible"
    :type="popupData.type"
    :data="popupData.row"
    @close="closePopup"
    @save="handleSave"
  />
</template>

<script>
// @ is an alias to /src
import NotificationPopup from "@/components/dashboard/popus/notificationPopup.vue";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  name: "SendNotifications",
  components: {
    NotificationPopup,
    SvgIcon,
  },
  data() {
    return {
      filter: "",
      rows: [
        {
          code: 1,
          photo: "",
          name: "  ",
          description: "     ",
          TheArea: "",
          sendTo: "",
          status: false,
        },
        // More products here
      ],
      popupData: {
        visible: false,
        type: "",
        row: null,
      },
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        if (!this.filter) return true;
        const searchTerm = this.filter.toLowerCase();
        return (
          (row.name && row.name.toLowerCase().includes(searchTerm)) ||
          (row.description &&
            row.description.toLowerCase().includes(searchTerm))
        );
      });
    },
  },
  mounted() {
    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
    openPopup(type, row) {
      this.popupData = {
        visible: true,
        type,
        row,
      };
    },
    updateStatus(item) {
      // Here you would typically make an API call to update the status on the server
      console.log("Updating status for item:", item);
      // Example:  axios.put(`/api/items/${item.id}`, { isActive: item.isActive });
    },
    closePopup() {
      this.popupData.visible = false;
    },
    handleSave(updatedRow) {
      if (this.popupData.type === "edit") {
        const index = this.rows.findIndex(
          (row) => row.code === updatedRow.code
        );
        if (index !== -1) {
          this.rows.splice(index, 1, updatedRow);
        }
      } else if (this.popupData.type === "add") {
        updatedRow.code =
          this.rows.length > 0
            ? Math.max(...this.rows.map((row) => row.code)) + 1
            : 1; // Increment code or start from 1 if no rows exist
        this.rows.push(updatedRow);
      } else if (this.popupData.type === "delete") {
        const index = this.rows.findIndex(
          (row) => row.code === updatedRow.code
        );
        if (index !== -1) {
          this.rows.splice(index, 1);
          if (this.$toast) {
            this.$toast.success("Notification deleted successfully!");
          }
        }
      }
      this.closePopup();
    },
  },
};
</script>
<style lang="scss" scoped>
.Control_panel {
  @media (min-width: 1199px) {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
  .header {
    display: flex;
    align-content: center;
    padding-top: 40px;
    width: 100%;
  }
  .content p {
    margin: 0;
    width: 200px !important;
    height: 27px;
    font-weight: 400;
    color: #000000;
    margin-top: 20px;
    line-height: 5px;
    font-size: 18px;
  }
}

.table-container {
  margin-top: 20px;
  height: fit-content;
  border-radius: 8px 8px 20px 20px;
  padding: 16px 24px 16px 24px;
  background-color: #ffffff;
  margin-left: 27px;
  thead {
    width: 1, 098px;
    height: 46px;
    border-bottom: 1px;

    background-color: #5c0e350a;
    border: 1px solid #e2e0e0;
    padding-left: 8px;
  }
  thead th {
    width: 121px;
    height: 46px;
    gap: 4px;
  }

  .headerTable {
    width: 1, 098px;
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    .icon_text {
      width: 50%;
      display: flex;
      justify-content: start;
      align-items: center;
      p {
        font-size: 20px;
        line-height: 16px;
        font-weight: 400;
        text-align: right;
        color: #000000;
      }
    }
    .search_add {
      width: 50%;
      display: flex;
      justify-content: end;
      align-items: center;
      .search {
        position: relative;
        border-radius: 24px;
        padding: 0 16px 0 16px;
        gap: 4px;
        border: 1px solid #e2e0e0;
        background-color: #ffffff;
        margin-left: 20px;
        input {
          height: 45px;
          border: none;
          outline: none;
          padding-right: 20px;
          margin-left: 20px;
        }
        .svg {
          position: absolute;
          right: 10px;
          top: 12px;
        }
      }
      .search-button {
        width: 151px;
        height: 45px;
        border-radius: 40px;
        border: none;

        background-color: var(--primary-color);
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        cursor: pointer;
        .addRestbtn {
          width: 109px;
          height: 21px;
          font-size: 14px;
          font-weight: 400;
          text-align: center;
          margin-top: 18px;
        }
        .icon {
          width: 20px;
          height: 20px;
          display: center;
          align-items: center;
        }
      }
    }
    .icon {
      width: 44px;
      height: 44px;
      border-radius: 120px;
      gap: 10px;
      background-color: #5c0e3514;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 10px;
      i {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border-style: stroke;
      }
    }
  }
  p {
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 700;
  }

  .table-responsive {
    overflow-x: auto;
    border-radius: 8px;
    background-color: #fff;

    table {
      width: 100%;
      border-collapse: collapse;
      border: none th, td {

      }

      th {
        text-align: left;

        /* Alignment for RTL */
        [dir="rtl"] & {
          text-align: right;
        }
        color: black;
      }
      tbody tr {
        width: 1, 098px;
        height: 56px;
      }
      td {
        width: 121px;
        height: 54px;
        button {
          margin: 0 5px;
          cursor: pointer;
        }
      }
    }
  }
}
.actions {
  display: flex !important;
  justify-content: start;
  gap: 10px;
  .iconsIctions {
    width: 122.5px;
    height: 44.17px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .icon {
      width: 44.17px;
      height: 44.17px;
 
      gap: 10px;
    }
    .icon .svg {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 10px;
      height: 10px;
      background-color: #5c0e350a;
      padding: 10px;
      border-radius: 50%;
    }
    .icon .svg.delete {
      background-color: #ff00002f;
      
    }
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
      .Control_panel {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
  .content {
    width: 200px;
    height: 27px;
  }
  .content p {
    font-size: 18px !important;
  }
  .content span {
    font-size: 13px !important;
  }
  .boxDetails {
    gap: 10px;
  }
}
@media (max-width: 992px) {
  thead th {
    font-size: 14px !important;
  }
}

@media (max-width: 992px) and (min-width: 1199px) {
  .table-container {
    width: 200px !important;
  }
  .search_add {
    .search {
      width: 150px;

      input {
        width: 100px !important;
      }
    }
  }
}
@media (max-width: 768px) and (min-width: 991px) {
  .Control_panel {
    margin: 0 !important;
  }
  .table-container {
    width: 0 !important;
    p {
      font-size: 14px !important;
    }
    .search_add {
      .search {
        width: 200px !important;
      }
    }
    .icon_text p {
      font-size: 14px !important;
    }
    .table-responsive {
      table {
        th,
        td {
          font-size: 12px;
        }
      }
    }
  }
}
/* Media Queries for Small Screens (below 768px) */
@media (max-width: 768px) {
  .Control_panel {
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding: 0 10px;
  }

  .boxDetails {
    flex-direction: column;
    align-items: center !important;
    margin: 5px !important;
    gap: 10px !important;
  }

  .dashboard {
    flex-direction: column;
    margin: 0 !important;
    height: auto !important;

    .summary-container,
    .chart-container {
      width: 90% !important;
      margin: 5px 0 !important;
      height: auto;
      padding: 15px;

      #orders-chart,
      #sales-purchases-chart {
        width: 100% !important;
        height: 200px !important;
      }
    }
  }

  .table-container {
    margin: 20px 0 0 0 !important;
    padding: 10px !important;
    overflow-x: auto;

    .headerTable {
      flex-direction: column;
      height: auto;
      gap: 15px;

      .icon_text,
      .search_add {
        width: 100% !important;
        justify-content: center !important;
        flex-direction: column;
      }

      .search {
        input {
        }
      }

      .search-button {
        width: 100% !important;
        margin-top: 10px;
      }
    }

    .table-responsive {
      overflow-x: auto;

      table {
        min-width: 600px;

        th,
        td {
          padding: 8px 4px;
          font-size: 12px;
        }
      }
    }
  }
}
.switch {
  position: relative;
  display: inline-block;
  width: 24px;
  height: 14px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider - the moving part */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 10px;
  width: 10px;
  left: 4px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #3fad4c;
}

input:checked + .slider:before {
  transform: translateX(10px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>
