<template>
  <div class="popup-overlay" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="popup-content">
      <div class="header">
        <div class="semi-header">
          <!-- SVG Icon (as before) -->
          <SvgIcon name="bannerIcon"></SvgIcon>
          <h3 v-if="type === 'view'">{{ $t("ViewBanner") }}</h3>
          <h3 v-else-if="type === 'edit'">{{ $t("EditBanner") }}</h3>
          <h3 v-else-if="type === 'add'">{{ $t("AddBanner") }}</h3>
          <h3 v-else>{{ $t("DeleteBanner") }}</h3>
        </div>
        <button class="close-button" @click="$emit('close')"></button>
      </div>
      <div v-if="type === 'delete'" class="delete-confirmation">
        <p>{{ $t("confirmDeleteBanner") }}</p>
        <p>{{ $t("deleteWarning") }}</p>
        <div class="popup-actions">
          <button
            style="background-color: #f02116; color: #ffffff"
            @click="confirmDelete"
          >
            {{ $t("delete") }}
          </button>
          <button @click="$emit('close')">{{ $t("Cancel") }}</button>
        </div>
      </div>
      <div v-else>
        <div class="form" v-if="type !== 'delete'">
          <div class="input-container">
            <label>{{ $t("BannerName") }}</label>
            <input v-model="localData.bannerName" :disabled="type === 'view'" />
          </div>

          <label style="">{{ $t("BannerImage") }}</label>
          <div v-if="type === 'view'" class="input-container file">
            <img
              :src="localData.photo"
              alt="Banner Image"
              class="preview-image"
            />
          </div>
          <div v-else class="input-container file">
            <label>{{ $t("AttachPicture_banner") }}</label>

            <!-- Modified File Input Area -->
            <div
              class="upload-area"
              @dragover.prevent="handleDragOver"
              @drop.prevent="handleDrop"
            >
              <div class="upload-icon">
                <!-- Replace with your desired icon (e.g., a cloud upload icon) -->
                <SvgIcon name="uploadImage"></SvgIcon>
              </div>
              <p>
                {{ $t("Drag_drop") }}
              </p>
              <p>{{ $t("Or") }}</p>
              <button @click="triggerFileInput" class="browse">
                {{ $t("BrowseFiles") }}
              </button>
            </div>
            <input
              type="file"
              style="display: none"
              @change="handleImageUpload"
              ref="fileInput"
            />
          </div>
        </div>
        <div class="popup-actions">
          <button @click="save">
            {{ type === "add" ? $t("add") : $t("Save") }}
          </button>
          <button @click="$emit('close')">{{ $t("Cancel") }}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  props: {
    type: String,
    data: Object,
  },
  components: {
    SvgIcon,
  },
  setup() {
    const toast = useToast();
    return { toast };
  },
  data() {
    return {
      localData: { ...this.data },
    };
  },
  methods: {
    handleDragOver(event) {
      event.preventDefault();
    },
    handleDrop(event) {
      event.preventDefault();
      const file = event.dataTransfer.files[0];
      this.processFile(file);
    },
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    handleImageUpload(event) {
      const file = event.target.files[0];
      this.processFile(file);
    },
    processFile(file) {
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.localData.photo = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    save() {
      // Reset errors
      this.errors = {
        bannerName: false,
        photo: false,
      };

      // Validate banner name
      if (
        !this.localData.bannerName ||
        this.localData.bannerName.trim() === ""
      ) {
        this.errors.bannerName = true;
        this.toast.error(this.$t("bannerNameRequired"));
        return;
      }

      // Validate image for add/edit operations
      if (
        (this.type === "add" || this.type === "edit") &&
        !this.localData.photo
      ) {
        this.errors.photo = true;
        this.toast.error(this.$t("bannerImageRequired"));
        return;
      }

      this.$emit("save", this.localData);
      this.toast.success(
        this.type === "add"
          ? this.$t("bannerAddedSuccessfully")
          : this.$t("bannerUpdatedSuccessfully")
      );
    },
    confirmDelete() {
      this.$emit("delete", this.localData);
      this.toast.success(this.$t("bannerDeletedSuccessfully"));
    },
  },
};
</script>
<style scoped>
.popup-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: #ffffff;
  border-radius: 16px;
  width: 610px;
  max-width: 90%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-button {
  background: none;
  border: none;
  color: #292d32;
  font-size: 25px;
  margin: 0 20px 0 20px;
  cursor: pointer;
  transition: color 0.3s;
  font-family: Almarai !important;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 12px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 90%;
  max-width: 559px;
  height: auto;
  min-height: 48px;
  border: none;
  padding: 12px 16px;
  border-radius: 40px;
  font-size: 14px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .popup-actions button {
    width: 100%;
    min-height: 42px;
    padding: 10px 12px;
    font-size: 13px;
  }

  .popup-actions {
    gap: 8px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
}

@media (max-width: 480px) {
  .popup-actions button {
    min-height: 38px;
    padding: 8px 10px;
    font-size: 12px;
  }
}

.popup-actions button:first-child {
  background-color: var(--primary-color);
  color: #ffffff;
  font-family: Almarai !important;
}

.popup-actions button:last-child {
  background-color: #ffffff;
    border: 1px solid #111111;

  color: #545454;
  font-family: Almarai !important;
}

ul {
  list-style: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

li {
  background-color: var(--primary-color);
  width: 116px;
  height: 35px;
  border-radius: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px 0 15px;
  color: #ffffff;
}

li button {
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  color: var(--primary-color);
  background-color: #ffffff;
  padding: 2px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 6px;
  font-family: Almarai !important;
  transition: color 0.3s;
}
.form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
.file {
  display: flex;
  justify-content: start;
  align-items: center;
  height: 200px; /* Increased height */
  margin: 20px 10px 20px 10px;
  border: 2px dashed #ddd; /* Added border */
  border-radius: 8px; /* Added border-radius */
}

.upload-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px;
  text-align: center;
  cursor: pointer;
}

.upload-icon {
  margin-bottom: 10px;
  color: #999;
}

.upload-area p {
  margin: 5px 0;
  color: #777;
}

.upload-area button {
  background-color: var(--primary-color);
  border: none;
  color: #ffffff;
  padding: 8px 16px;
  font-family: Almarai !important;
  gap: 5px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.input-container .ph {
  height: 100px;
  width: 45%;
}
.input-container img {
  width: fit-content;
  height: 100%;
}
.preview-image {
  margin-top: 10px;
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
}
.delete-confirmation {
  text-align: center;
  padding: 20px;
}
</style>
