<template>
  <aside class="vertical-nav" ref="aside">
    <div>
      <div class="logo">
        <img src="../assets/logo/logo guid line.png" alt="logo" />
      </div>
      <ul class="list-unstyled">
        <router-link :to="{ name: 'home' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="home"></SvgIcon>
              <!-- Dashboard Icon -->
              <span>{{ $t("dachbord") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'OrderManagement' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="OrderManagement"></SvgIcon>
              <span>{{ $t("Aside_LI2") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'ProductManagement' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="ProductManagement"></SvgIcon>
              <span>{{ $t("Aside_LI3") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'CustomerManagement' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="CustomerManagement"></SvgIcon>
              <span>{{ $t("Aside_LI4") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'RestaurantManagement' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="RestaurantManagement"></SvgIcon>
              <span>{{ $t("Aside_LI5") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'ReportsAndAnalytics' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="ReportsAndAnalytics"></SvgIcon>
              <span>{{ $t("Aside_LI6") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'ratings' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="ratings"></SvgIcon>
              <span>{{ $t("Aside_LI7") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'AddBanners' }">
          <li>
            <div class="section-name">
              <SvgIcon class="svg" name="AddBanners"></SvgIcon>
              <span>{{ $t("Aside_LI8") }}</span>
            </div>
          </li>
        </router-link>
        <router-link :to="{ name: 'SendNotifications' }">
          <li>
            <div class="section-name">
              <font-awesome-icon :icon="['far', 'bell']" class="f-awesome" />
              <span>{{ $t("Aside_LI9") }}</span>
            </div>
          </li>
        </router-link>

        <div
          @click="toggleMenu"
          class="settings"
          :class="{ activeClass: isAActive }"
          :style="isAActive ? activeStyle : defaultStyle"
        >
          <li class="settings-menu">
            <div>
              <font-awesome-icon :icon="['fas', 'gear']" class="icon-gear" />
              <span>{{ $t("Aside_LI10") }}</span>
            </div>
            <font-awesome-icon
              :icon="['fas', isOpen ? 'chevron-up' : 'chevron-down']"
              class="icon-chevron"
            />
          </li>
        </div>
        <!-- Menu Items -->
        <div v-if="isOpen" @click="Help_Center" class="menu-item">
          <li>
            <span class="menu-text">{{ $t("Help_Center") }}</span>
            <font-awesome-icon
              :icon="['fas', 'circle']"
              :class="{ active: isActive('Help_Center') }"
              class="icon-diamond"
            />
          </li>
        </div>
        <div v-if="isOpen" @click="privacy_policy" class="menu-item">
          <li>
            <span class="menu-text">{{ $t("privacy_policy") }}</span>
            <font-awesome-icon
              :icon="['fas', 'circle']"
              :class="{ active: isActive('privacy_policy') }"
              class="icon-diamond"
            />
          </li>
        </div>
        <div v-if="isOpen" @click="settings" class="menu-item">
          <li>
            <span class="menu-text">{{ $t("Aside_LI11") }}</span>
            <font-awesome-icon
              :icon="['fas', 'circle']"
              :class="{ active: isActive('settings') }"
              class="icon-diamond"
            />
          </li>
        </div>
        <div v-if="isOpen" @click="About_Us" class="menu-item">
          <li>
            <span class="menu-text">{{ $t("About_Us") }}</span>
            <font-awesome-icon
              :icon="['fas', 'circle']"
              :class="{ active: isActive('About_Us') }"
              class="icon-diamond"
            />
          </li>
        </div>
      </ul>
    </div>
  </aside>
  <teleport to="body">
    <div :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
      <div class="asideToggler" @click="openAside">
        <SvgIcon class="svg" name="asideToggler"></SvgIcon>
      </div>
    </div>
  </teleport>
</template>

<script>
import SvgIcon from "@/components/SvgIcon.vue";
export default {
  name: "SettingsMenu",
  components: {
    SvgIcon,
  },
  data() {
    return {
      isOpen: false,
      isAActive: false,
      activeStyle: {
        borderRadius: "6px",
        backgroundColor: "var(--primary-color)",
        color: "white",
      },
    };
  },
  mounted() {
    // Add event listener to handle screen resizing
    window.addEventListener("resize", this.handleResize);
    // Call the method initially to set the correct state
    this.handleResize();
  },
  beforeUnmount() {
    // Remove event listener when the component is destroyed
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    openAside() {
      this.$refs.aside.classList.toggle("open");
    },
    handleResize() {
      if (window.innerWidth >= 991) {
        this.$refs.aside.classList.add("open");
      } else {
        this.$refs.aside.classList.remove("open");
      }
    },
    toggleMenu() {
      this.isOpen = !this.isOpen;
      this.isAActive = !this.isAActive;
    },
    isActive(routeName) {
      return this.$route.name === routeName;
    },
    Help_Center() {
      this.$router.push({ name: "Help_Center" });
    },
    privacy_policy() {
      this.$router.push({ name: "privacy_policy" });
    },
    settings() {
      this.$router.push({ name: "settings" });
    },

    About_Us() {
      this.$router.push({ name: "About_Us" });
    },
  },
};
</script>

<style lang="scss" scoped>
.vertical-nav {
  position: fixed;
  top: 32px;
  width: 285px;
  height: 640px; // Full viewport height for responsiveness
  border-radius: 24px;
  text-align: right;
  overflow-y: auto;
  background-color: #fff;
  padding: 10px;
  transition: right 0.4s ease-out !important; // Smooth transition for the left property
  &.open {
    right: start; // Slide into view when open
  }

  @media (max-width: 991px) {
    z-index: 999;
    top: -660px; // Hidden by default on smaller screens
    &.open {
      top: 32px; // Slide in when open
    }
  }

  .logo {
    padding-top: 10px;
    padding-right: 16px;
    margin-bottom: 10px;
  }
  .logo img {
    width: 72.08px;
    height: 47px;
  }
  ul {
    padding: 0;
    display: grid;
    gap: 11px;
    margin: 0;
    a {
      border-radius: 12px;
      text-decoration: none;
      text-align: right;
      color: black;
    }
    a:hover {
      background-color: var(--primary-color);
      color: white;
    }

    li {
      padding-left: 16px;
      padding-right: 16px;
      border-radius: 6px;
      padding-top: 5px;
      padding-bottom: 5px;
      cursor: pointer;
      &:hover svg {
        fill: #ffffff !important;
      }

      &:hover .f-awesome {
        color: #ffffff !important;
      }

      .section-name {
        display: flex;
        justify-content: start;
        align-content: center;
        margin-right: 5px;
      }
      .section-name .svg {
        width: 16.13px;
        height: 16.13px;
        margin-left: 10px;
        margin-right: 10px;
        fill: #545454;
      }
      .section-name .f-awesome {
        width: 16.13px;
        height: 16.13px;
        margin-left: 10px;
        margin-right: 10px;
        color: #545454;
      }

      span {
        font-size: 16px;
        font-weight: 400px;
      }

      &.second-li {
        span {
          font-size: 0.9rem;
          font-weight: 340;
        }
      }

      span {
        font-size: 1.1rem;
        font-weight: 500;
      }
    }
  }
}
.asideToggler {
  left: 10px;
  svg {
    background-color: #ffffff !important;
  }
  // Adjust for RTL direction
  [dir="rtl"] & {
    left: auto; // Reset left
    right: 10px; // Set right for RTL
  }

  position: absolute;
  top: 3px;
  border-radius: 5px;
  z-index: 1000;
  padding: 5px 15px;
  color: #ffffff;
  cursor: pointer;
  display: none;
  background-color: var(--primary-color);

  svg {
    font-size: 10px;
  }

  @media (max-width: 991px) {
    display: block;
    margin-bottom: 10px;
  }
}

.router-link-exact-active {
  background-color: var(--primary-color);
  color: white !important;
  .svg {
    fill: white !important; // Ensure icons match the text color
  }
  .section-name .f-awesome {
    color: white !important;
  }
}
/* Container Styling */
.settings-menu {
  margin: 0 12px 0 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-title {
  font-size: 16px;
  font-weight: bold;
}

.icon-chevron,
.icon-gear {
  font-size: 16px;
  margin: 0 5px 0 5px;
}

/* Menu List Styling */
.settings-list {
  background-color: transparent !important;
  list-style-type: none;
  padding: 0;
  margin-top: 10px;
  overflow: hidden; /* Hide content when closed */
  transition: height 0.3s ease; /* Add transition for smooth open/close */
}

/* Menu Item Styling */
.menu-item {
  text-decoration: none;
  display: block;
  color: #111111;
  padding: 0 40px 0 15px;
}

.menu-item li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
}
.menu-item:hover .icon-diamond {
  color: var(--primary-color); /* Matches active diamond color */
}
.menu-text {
  font-size: 14px;
}

.icon-diamond {
  font-size: 12px;
}

.icon-diamond.active {
  color: var(--primary-color); /* Matches active diamond color */
}

.icon-diamond:not(.active) {
  color: #cccccc; /* Matches inactive diamond color */
}
.settings:hover {
  background-color: var(--primary-color);
  color: #ffffff;
  border-radius: 6px;
}
</style>
