<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="boxDetails">
        <boxDetails />
      </div>
      <LoadingHere :isLoading="isLoading" />

      <div class="table-container">
        <div class="headerTable">
          <div class="icon_text">
            <div class="icon">
              <SvgIcon name="icon-container2"></SvgIcon>
            </div>
            <p>{{ $t("customers") }}</p>
          </div>
          <div class="search_add">
            <div class="search">
              <input type="text" v-model="filter" :placeholder="$t('search')" />
              <SvgIcon class="svg" name="search"></SvgIcon>
            </div>
            <button class="search-button" @click="openPopup('add')">
              <p class="addRestbtn">{{ $t("AddCust") }}</p>
              <span class="icon">
                <SvgIcon name="add"></SvgIcon>
              </span>
            </button>
          </div>
        </div>
        <div class="table-responsive">
          <table>
            <thead>
              <tr class="table-header">
                <th>
                  <div
                    style="
                      display: flex;
                      align-items: center;
                      margin-right: 10px;
                      gap: 5px;
                    "
                  >
                    <SvgIcon name="circle"></SvgIcon>

                    {{ $t("head2Table2") }}
                  </div>
                </th>
                <th>{{ $t("head2Table3") }}</th>
                <th>{{ $t("head4Table3") }}</th>
                <th>{{ $t("headTable4") }}</th>
                <th>{{ $t("head4Table5") }}</th>
                <th>{{ $t("head4Table6") }}</th>
                <th>{{ $t("AllProcedures") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(rows, index) in filteredRows"
                :key="`product-${index}`"
              >
                <td
                  style="
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                    gap: 5px;
                  "
                >
                  <SvgIcon name="dot_circle"></SvgIcon>

                  {{ rows.customerName }}
                </td>
                <td>{{ rows.phone }}</td>
                <td>{{ rows.email }}</td>
                <td>{{ rows.ordersCount }}</td>
                <td>{{ rows.ordersTotal }}</td>
                <td>{{ rows.created_a }}</td>
                <td class="actions">
                  <div class="iconsIctions">
                    <div class="icon" @click="editCustomer(index)">
                      <SvgIcon class="svg" name="edit"></SvgIcon>
                    </div>
                    <div class="icon" @click="viewCustomer(index)">
                      <SvgIcon class="svg" name="view"></SvgIcon>
                    </div>

                    <div class="icon" @click="confirmDeleteCustomer(index)">
                      <!-- Delete Icon -->
                      <SvgIcon class="svg delete" name="deleteItem"></SvgIcon>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <customerPopup
    v-if="isPopupOpen"
    :mode="popupMode"
    :customer="selectedCustomer"
    @close="closePopup"
    @save="saveCustomer"
    @delete="handleDeleteConfirmation"
  />
</template>

<script>
// @ is an alias to /src
import boxDetails from "@/components/dashboard/boxDetails.vue";
import customerPopup from "@/components/dashboard/popus/customerPopup.vue";
import axios from "axios";
import SvgIcon from "@/components/SvgIcon.vue";
import LoadingHere from "@/components/LoadingHere.vue"; // Import LoadingHere component

export default {
  name: "CustomerManagement",
  components: {
    boxDetails,
    LoadingHere,
    customerPopup,
    SvgIcon,
  },
  data() {
    return {
      filter: "",
      rows: [
        // More products here
      ],
      isPopupOpen: false,
      isLoading: false, // Add loading state

      selectedCustomer: null,
      popupMode: "add", // 'add', 'edit', 'view', 'delete'
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          (row.customerName &&
            String(row.customerName).toLowerCase().includes(searchTerm)) || // Ensure 'customerName' exists and is a string
          (row.phone && String(row.phone).toLowerCase().includes(searchTerm)) || // Ensure 'phone' exists and is a string
          (row.email && String(row.email).toLowerCase().includes(searchTerm)) // Ensure 'email' exists and is a string
        );
      });
    },
  },
  mounted() {
    this.isLoading = true;
    this.FetchRows(); // Fetch rows dynamically
  },
  methods: {
    FetchRows() {
      const token = localStorage.getItem("authToken");
      axios
        .get("https://backend.alaqdk.com/api/admin/dashboard/get-all-users", {
          headers: {
            "Accept-Language": "ar",
            Authorization: `Bearer ${token}`, // Add the Authorization header
          },
        })
        .then((response) => {
           this.isLoading = false;
          console.log("Restaurant Response:", response.data); // Log the response
          const data = response.data;
          if (data.status) {
            this.rows = data.data.map((cust) => ({
              id: cust.user.id || Date.now() + Math.random(), // assign id for deletion
              customerName: cust.user.name,
              phone: cust.user.phone || "N/A",
              email: cust.user.email || "N/A",
              ordersCount: cust.ordersCount || 0,
              ordersTotal: cust.ordersTotal || 0,
              created_a: cust.user.created_at || "N/A",
            }));
            console.log(this.rows);
          } else {
            console.error("Failed to fetch rows data:", data.message);
          }
        })
        .catch((error) => {
           this.isLoading = false;
          console.error("Error fetching rows data:", error);
        });
    },
    openPopup(mode, customer = null) {
      this.popupMode = mode;
      this.selectedCustomer = customer || {
        customerName: "",
        phone: "",
        email: "",
        ordersCount: 0,
        ordersTotal: 0, // Use ordersTotal instead of TotalSpending
        LastOrderDate: new Date().toISOString().split("T")[0], // Default to today's date
      };
      this.isPopupOpen = true;
    },
    closePopup() {
      this.isPopupOpen = false;
    },
    saveCustomer(customer) {
      if (this.popupMode === "edit" && customer.id) {
        // Find the index of the customer to update using the id
        const index = this.rows.findIndex((row) => row.id === customer.id);
        if (index !== -1) {
          this.rows[index] = { ...this.rows[index], ...customer }; // Update the row
        }
      } else if (this.popupMode === "add") {
        // Assign a unique ID to the new customer and add it to the rows
        customer.id = Date.now();
        customer.created_a = customer.LastOrderDate; // Set the date in the table
        this.rows.push(customer);
      }
      this.closePopup();
    },
    deleteCustomer(customerId) {
      // Filter out the row with the matching id
      this.rows = this.rows.filter((row) => row.id !== customerId);
      this.closePopup();
    },
    viewCustomer(index) {
      this.openPopup("view", { ...this.rows[index] });
    },
    editCustomer(index) {
      const customer = this.rows[index];
      this.openPopup("edit", { ...customer }); // Pass the customer object for editing
    },
    confirmDeleteCustomer(index) {
      const customer = this.rows[index];
      this.openPopup("delete", { ...customer }); // Pass the customer object for deletion confirmation
    },
    handleDeleteConfirmation() {
      if (this.selectedCustomer && this.selectedCustomer.id) {
        this.deleteCustomer(this.selectedCustomer.id); // Use the id to delete the customer
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.Control_panel {
  @media (min-width: 1199px) {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
}

.boxDetails {
  display: flex;
  justify-content: space-evenly;
  gap: 20px;
  margin-left: 30px;
  margin-right: 10px;
}

.table-container {
  margin-top: 20px;
  height: fit-content;
  border-radius: 8px 8px 20px 20px;
  padding: 16px 24px 16px 24px;
  background-color: #ffffff;
  margin-left: 27px;
  thead {
    width: 1, 098px;
    height: 46px;
    border-bottom: 1px;

    background-color: #5c0e350a;
    border: 1px solid #e2e0e0;
    padding-left: 8px;
  }
  thead th {
    width: 121px;
    height: 46px;
    gap: 4px;
  }

  .headerTable {
    width: 1, 098px;
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    .icon_text {
      width: 50%;
      display: flex;
      justify-content: start;
      align-items: center;
      p {
        font-size: 20px;
        line-height: 16px;
        font-weight: 400;
        text-align: right;
        color: #000000;
      }
    }
    .search_add {
      width: 50%;
      display: flex;
      justify-content: end;
      align-items: center;
      .search {
        position: relative;
        border-radius: 24px;
        padding: 0 16px 0 16px;
        gap: 4px;
        border: 1px solid #e2e0e0;
        background-color: #ffffff;
        margin-left: 20px;
        input {
          height: 45px;
          border: none;
          outline: none;
          padding-right: 20px;
          margin-left: 20px;
        }
        .svg {
          position: absolute;
          right: 10px;
          top: 12px;
        }
      }
      .search-button {
        width: 151px;
        height: 45px;
        border: none;

        border-radius: 40px;
        background-color: var(--primary-color);
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        cursor: pointer;
        .addRestbtn {
          width: 109px;
          height: 21px;
          font-size: 14px;
          font-weight: 400;
          text-align: center;
          margin-top: 18px;
        }
        .icon {
          width: 20px;
          height: 20px;
          display: center;
          align-items: center;
        }
      }
    }
    .icon {
      width: 44px;
      height: 44px;
      border-radius: 120px;
      gap: 10px;
      background-color: #5c0e3514;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 10px;
      i {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border-style: stroke;
      }
    }
  }
  p {
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 700;
  }

  .table-responsive {
    overflow-x: auto;
    border-radius: 8px;
    background-color: #fff;

    table {
      width: 100%;
      border-collapse: collapse;
      border: none th, td {

      }

      th {
        text-align: left;

        /* Alignment for RTL */
        [dir="rtl"] & {
          text-align: right;
        }
        color: black;
      }
      tbody tr {
        width: 1, 098px;
        height: 56px;
      }
      td {
        width: 121px;
        height: 54px;
        button {
          margin: 0 5px;
          cursor: pointer;
        }
      }
    }
  }
}
.actions {
  display: flex !important;
  justify-content: start;
  gap: 10px;
  .iconsIctions {
    width: 122.5px;
    height: 44.17px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .icon {
      width: 44.17px;
      height: 44.17px;

      gap: 10px;
    }
    .icon .svg {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 10px;
      height: 10px;
      background-color: #5c0e350a;
      padding: 10px;
      border-radius: 50%;
    }
    .icon .svg.delete {
      background-color: #ff00002f;
    }
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .Control_panel {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }

  .boxDetails {
    gap: 10px;
  }
}
@media (max-width: 992px) {
  thead th {
    font-size: 14px !important;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .table-container {
    width: 200px !important;
  }
  .search_add {
    .search {
      width: 150px;

      input {
        width: 100px !important;
      }
    }
  }
}
@media (max-width: 768px) and (min-width: 991px) {
  .Control_panel {
    margin: 0 !important;
  }
  .table-container {
    width: 0 !important;
    p {
      font-size: 14px !important;
    }
    .search_add {
      .search {
        width: 200px !important;
      }
    }
    .icon_text p {
      font-size: 14px !important;
    }
    .table-responsive {
      table {
        th,
        td {
          font-size: 12px;
        }
      }
    }
  }
}
/* Media Queries for Small Screens (below 768px) */
@media (max-width: 768px) {
  .Control_panel {
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding: 0 10px;
  }

  .boxDetails {
    flex-direction: column;
    align-items: center !important;
    margin: 5px !important;
    gap: 10px !important;
  }

  .dashboard {
    flex-direction: column;
    margin: 0 !important;
    height: auto !important;

    .summary-container,
    .chart-container {
      width: 90% !important;
      margin: 5px 0 !important;
      height: auto;
      padding: 15px;

      #orders-chart,
      #sales-purchases-chart {
        width: 100% !important;
        height: 200px !important;
      }
    }
  }

  .table-container {
    margin: 0 !important;
    padding: 10px !important;
    overflow-x: auto;

    .headerTable {
      flex-direction: column;
      height: auto;
      gap: 15px;

      .icon_text,
      .search_add {
        width: 100% !important;
        justify-content: center !important;
        flex-direction: column;
      }

      .search {
        input {
        }
      }

      .search-button {
        width: 100% !important;
        margin-top: 10px;
      }
    }

    .table-responsive {
      overflow-x: auto;

      table {
        min-width: 600px;

        th,
        td {
          padding: 8px 4px;
          font-size: 12px;
        }
      }
    }
  }
}
</style>
