<template>
  <div class="popup-overlay" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="popup-content">
      <div class="header">
        <div class="semi-header">
          <SvgIcon name="bannerIcon"></SvgIcon>

          <h3 v-if="type === 'view'">{{ $t("") }}</h3>
          <h3 v-else>{{ $t("ratingDelete") }}</h3>
        </div>
        <button class="close-button" @click="$emit('close')">X</button>
      </div>
      <div v-if="type === 'view'">
        <div class="input-container">
          <label>{{ $t("CustomerName") }}</label>
          <input v-model="formData.customerName" disabled />
        </div>
        <div class="input-container">
          <label>{{ $t("Rating") }}</label>
          <input v-model="formData.rating" disabled />
        </div>
        <div class="input-container">
          <label>{{ $t("Comment") }}</label>
          <textarea v-model="formData.comment" disabled></textarea>
        </div>
        <div class="input-container">
          <label>{{ $t("Resident Producer") }}</label>
          <input v-model="formData.ResidentProducer" disabled />
        </div>
        <div class="input-container">
          <label>{{ $t("Date") }}</label>
          <input type="date" v-model="formData.date" disabled />
        </div>
      </div>

      <p v-if="type === 'delete'" style="text-align: center">
        {{ $t("confirmDeleteRating") }}
      </p>
      <p v-if="type === 'delete'" style="text-align: center; color: #545454">
        {{ $t("deleteWarning") }}
      </p>

      <div class="popup-actions">
        <button
          style="background-color: #f02116; color: #ffffff"
          v-if="type === 'delete'"
          @click="handleDelete"
        >
          {{ $t("delete") }}
        </button>
        <button @click="$emit('close')">{{ $t("Cancel") }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  components: {
    SvgIcon,
  },
  props: {
    type: { type: String, required: true }, // 'view', 'delete'
    data: { type: Object, default: () => ({}) },
  },
  data() {
    return {
      formData: { ...this.data },
      toast: null,
    };
  },
  created() {
    this.toast = useToast();
  },
  methods: {
    handleDelete() {
      this.$emit("save", null);
      if (this.toast) {
        this.toast.success(this.$t("Rating deleted successfully"));
      }
    },
  },
};
</script>

<style scoped>
.popup-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: #ffffff;
  border-radius: 16px;
  width: 610px;
  max-width: 90%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-button {
  background: none;
  border: none;
  color: #292d32;
  font-family: Almarai !important;
  font-size: 25px;
  margin: 0 20px 0 20px;
  cursor: pointer;
  transition: color 0.3s;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 12px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 90%;
  max-width: 559px;
  height: 58px;
  padding: 8px 16px;
  border-radius: 40px;
  font-size: 14px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .popup-actions button {
    width: 95%;
    height: 50px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .popup-actions button {
    width: 100%;
    height: 45px;
    font-size: 12px;
    padding: 6px 12px;
  }
}

.popup-actions button:first-child {
  background-color: var(--primary-color);
  font-family: Almarai !important;
  color: #ffffff;
}

.popup-actions button:last-child {
  background-color: #ffffff;
    border: 1px solid #111111;

  color: #545454;
  font-family: Almarai !important;
}

ul {
  list-style: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

li {
  background-color: var(--primary-color);
  width: 116px;
  height: 35px;
  border-radius: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px 0 15px;
  color: #ffffff;
}

li button {
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  color: var(--primary-color);
  background-color: #ffffff;
  padding: 2px;
  font-size: 16px;
  cursor: pointer;

  border-radius: 6px;
  transition: color 0.3s;
}
.form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
}
</style>
