<template>
  <div class="popup-overlay" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="popup-content">
      <!-- Handle 'edit' and 'add' popups -->
      <div v-if="type === 'edit' || type === 'add'">
        <div class="header">
          <div class="semi-header">
            <SvgIcon name="bannerIcon"></SvgIcon>
            <h3>
              {{
                type === "edit" ? $t("editRestaurant") : $t("addNewRestaurant")
              }}
            </h3>
          </div>
          <button class="close-btn" @click="$emit('close')">X</button>
        </div>
        <form @submit.prevent="submitForm">
          <div
            class="input-container"
            v-for="(value, key) in formData"
            :key="key"
          >
            <label>{{ $t(key) }}:</label>
            <input
              v-model.number="formData[key]"
              :type="
                key === 'lastPurchase'
                  ? 'date'
                  : typeof value === 'number'
                  ? 'number'
                  : 'text'
              "
              :placeholder="
                $t('enter' + key.charAt(0).toUpperCase() + key.slice(1))
              "
              :disabled="type === 'view'"
              @blur="validateField(key)"
              :class="{ error: errors[key] }"
            />
            <div class="error-message" v-if="errors[key]">
              {{ errors[key] }}
            </div>
          </div>
          <div class="popup-actions">
            <button v-if="type !== 'view'" type="submit">
              {{ $t(type === "edit" ? "save" : "add") }}
            </button>
            <button v-else @click="$emit('close')">{{ $t("close") }}</button>
          </div>
        </form>
      </div>

      <!-- Handle 'view' popup -->
      <div v-else-if="type === 'view'">
        <div class="header">
          <div class="semi-header">
            <SvgIcon name="bannerIcon"></SvgIcon>
            <h3>{{ $t("viewRestaurantDetails") }}</h3>
          </div>
          <button class="close-btn" @click="$emit('close')">X</button>
        </div>
        <div
          style="margin: 10px"
          class="input-container"
          v-for="(value, key) in formData"
          :key="key"
        >
          <label>{{ $t(key) }}:</label>
          <input
            v-model="formData[key]"
            :type="
              key === 'lastPurchase'
                ? 'date'
                : typeof value === 'number'
                ? 'number'
                : 'text'
            "
            disabled
          />
        </div>
        <div class="popup-actions">
          <button @click="$emit('close')">{{ $t("close") }}</button>
        </div>
      </div>

      <!-- Handle 'delete' popup -->
      <div v-else-if="type === 'delete'">
        <div class="popup-actionsDelete">
          <div class="icon">
            <SvgIcon name="delete"></SvgIcon>
          </div>
          <p>{{ $t("deleteRestaurant") }}</p>
          <span>{{ $t("deleteWarning") }}</span>
          <div class="btns">
            <button @click="confirmDelete">{{ $t("delete") }}</button>
            <button @click="$emit('close')">{{ $t("cancel") }}</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  components: {
    SvgIcon,
  },
  props: {
    type: { type: String, required: true },
    row: { type: Object, required: true },
  },
  setup() {
    const toast = useToast();
    return { toast };
  },
  data() {
    return {
      formData: {},
      errors: {},
    };
  },

  watch: {
    row: {
      immediate: true,
      handler(newRow) {
        // Initialize formData for 'add' and 'edit' popups
        this.formData =
          this.type === "add" ? this.getEmptyFormData() : { ...newRow };
      },
    },
  },
  methods: {
    validateForm() {
      this.errors = {};
      let isValid = true;

      if (!this.formData.restName?.trim()) {
        this.errors.restName = this.$t("fieldRequired");
        isValid = false;
      }

      if (
        this.formData.productCount == null ||
        this.formData.productCount === "" ||
        this.formData.productCount < 0
      ) {
        this.errors.productCount = this.$t("minValueError");
        isValid = false;
      }

      if (
        this.formData.orderCount == null ||
        this.formData.orderCount === "" ||
        this.formData.orderCount < 0
      ) {
        this.errors.orderCount = this.$t("minValueError");
        isValid = false;
      }

      if (!this.formData.lastPurchase) {
        this.errors.lastPurchase = this.$t("fieldRequired");
        isValid = false;
      }

      return isValid;
    },
    getEmptyFormData() {
      return {
        id: null, // ID will be assigned in the parent component for new rows
        restName: "",
        productCount: 0,
        orderCount: 0,
        lastPurchase: "",
      };
    },
    validateField(field) {
      if (field === "restName" && !this.formData.restName?.trim()) {
        this.errors.restName = this.$t("fieldRequired");
      } else if (
        (field === "productCount" || field === "orderCount") &&
        (this.formData[field] == null ||
          this.formData[field] === "" ||
          this.formData[field] < 0)
      ) {
        this.errors[field] = this.$t("minValueError");
      } else if (field === "lastPurchase" && !this.formData.lastPurchase) {
        this.errors.lastPurchase = this.$t("fieldRequired");
      } else {
        delete this.errors[field];
      }
    },
    submitForm() {
      if (!this.validateForm()) {
        this.toast.error(this.$t("formValidationError"));
        return;
      }

      try {
        this.$emit("save", { ...this.formData });
        this.toast.success(
          this.$t(
            this.type === "edit" ? "restaurantUpdated" : "restaurantAdded"
          )
        );
      } catch (error) {
        this.toast.error(this.$t("saveError"));
      }
    },
    confirmDelete() {
      this.$emit("save", { ...this.row }); // Emit a copy of the row for deletion
      this.toast.success(this.$t("deleteSuccessMessage"));
    },
  },
};
</script>

<style scoped>
.error {
  border-color: #ff4444;
}

.error-message {
  color: #ff4444;
  font-size: 12px;
  margin-top: 4px;
}

.popup-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.popup-content {
  background: #ffffff;
  border-radius: 16px;
  width: 90%;
  max-width: 610px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

@media (max-width: 768px) {
  .popup-content {
    width: 95%;
  }

  .popup-actions button,
  .popup-actionsDelete .btns button {
    width: 100% !important;
    max-width: 300px;
    min-height: 48px;
    padding: 12px;
    font-size: 16px;
  }

  .popup-actions {
    gap: 8px;
    margin: 15px 0;
  }

  .popup-actionsDelete .btns {
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }

  .input-container {
    margin: 8px 0;
  }

  input,
  select,
  textarea {
    width: 100%;
  }

  .semi-header {
    margin: 0 10px;
  }

  .close-btn {
    margin: 0 10px;
    font-size: 20px;
    padding: 8px;
  }

  .popup-actionsDelete p,
  .popup-actionsDelete span {
    width: 100%;
    padding: 0 10px;
    font-size: 14px;
  }
}

/* Extra small devices (phones, 600px and down) */
@media (max-width: 600px) {
  .popup-actions button,
  .popup-actionsDelete .btns button {
    max-width: 100%;
    min-height: 44px;
    font-size: 15px;
  }

  .close-btn {
    font-size: 18px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-btn {
  background: none;
  border: none;
  color: #292d32;
  font-size: 25px;
  margin: 0 20px 0 20px;
  cursor: pointer;
  transition: color 0.3s;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 12px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 559px;
  height: 58px;
  padding: 8px 16px;
  border-radius: 40px;
  background-color: var(--primary-color);
  font-size: 14px;
  cursor: pointer;
  border: none;
  color: #ffffff;
}

form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
}
.popup-actionsDelete {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
.popup-actionsDelete .icon {
  width: 100px;
  height: 94px;
  border-radius: 50%;
  background-color: #f02116;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup-actionsDelete p {
  color: #000000;
  font-size: 16px;
  font-weight: 700;
  width: 441px;
  height: 22px;
  text-align: center;
}
.popup-actionsDelete span {
  color: #8a8a8a;
  font-size: 16px;
  font-weight: 400;
  width: 441px;
  height: 22px;
  text-align: center;
}
.popup-actionsDelete .btns {
  margin: 20px 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.popup-actionsDelete .btns button:first-child {
  background-color: #f02116;
  color: #ffffff;
  width: 200px;
  height: 50px;
  border: none;

  border-radius: 40px;
  font-size: 16px;
  outline: none;
  font-weight: 400;
  font-family: Almarai !important;
}
.popup-actionsDelete .btns button:last-child {
  background-color: #e6e6e6;
  color: #333333;
  width: 200px;
  border: none;
  height: 50px;
  border-radius: 40px;
  outline: none;
  font-size: 16px;
  font-weight: 400;
  font-family: Almarai !important;
}
</style>
