<template>
  <div class="popup-overlay" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="popup-content">
      <div class="header">
        <div class="semi-header">
          <SvgIcon name="bannerIcon"></SvgIcon>

          <h3>
            {{
              mode === "add"
                ? $t("AddProduct")
                : mode === "edit"
                ? $t("EditProduct")
                : $t("ViewProduct")
            }}
          </h3>
        </div>
        <button class="close-button" @click="$emit('close')">X</button>
      </div>
      <form v-if="mode !== 'delete'" @submit.prevent="save">
        <div class="input-container">
          <label>{{ $t("ElementName") }}</label>
          <input v-model="formData.name" :disabled="mode === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("ItemCategory") }}</label>
          <input
            v-if="mode === 'view'"
            v-model="formData.productCategory"
            disabled
          />
          <select v-else v-model="formData.productCategory">
            <option value="">{{ $t("SelectCategory") }}</option>
            <option value="Juices">{{ $t("Juices") }}</option>
            <option value="Snacks">{{ $t("Snacks") }}</option>
            <option value="Dairy">{{ $t("Dairy") }}</option>
            <option value="IceCream">{{ $t("IceCream") }}</option>
            <option value="Drinks">{{ $t("Drinks") }}</option>
            <option value="Pizza">{{ $t("Pizza") }}</option>
            <option value="Seafood">{{ $t("Seafood") }}</option>
            <!-- Add more categories as needed -->
          </select>
        </div>
        <div class="input-container">
          <label>{{ $t("LastPurchase") }}</label>
          <input
            v-model="formData.LastPurchase"
            :disabled="mode === 'view'"
            :type="mode === 'view' ? 'text' : 'date'"
          />
        </div>
        <div
          class="input-container file"
          v-if="mode === 'add' || mode === 'edit'"
          @dragover.prevent
          @drop="handleDrop"
        >
          <label>{{ $t("AttachPicture_Product") }}</label>
          <div class="upload-area" @click="triggerFileInput">
            <input
              type="file"
              ref="fileInput"
              @change="handleImageUpload"
              style="display: none"
            />
            <div class="upload-icon">
              <!-- Replace with your upload icon -->
              <SvgIcon name="uploadImage"></SvgIcon>
            </div>
            <div class="upload-text">
              {{ mode === "add" ? $t("Drag_drop") : $t("uploadImage") }}
            </div>
          </div>
        </div>
        <div class="input-container" v-if="mode === 'view'">
          <label>{{ $t("productImage") }}</label>
          <img
            v-if="formData.cover_image"
            :src="formData.cover_image"
            alt="Product Image"
            class="preview-image"
          />
          <p v-else>{{ $t("NoImage") }}</p>
        </div>
        <div class="popup-actions">
          <button type="submit" v-if="mode !== 'view'">{{ $t("Save") }}</button>
          <span></span>
        </div>
      </form>
      <div v-else>
        <p style="text-align: center">{{ $t("confirmDeleteProduct") }}</p>
        <p style="text-align: center; color: #545454">
          {{ $t("deleteWarning") }}
        </p>
        <div class="popup-actions">
          <button
            style="background-color: #f02116; color: #ffffff"
            @click="deleteRow"
          >
            {{ $t("delete") }}
          </button>
        </div>
      </div>
      <div class="popup-actions">
        <button @click="$emit('close')">{{ $t("close") }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  components: {
    SvgIcon,
  },
  setup() {
    const toast = useToast();
    return { toast };
  },
  props: {
    mode: { type: String, required: true },
    rowData: { type: Object, default: () => ({}) },
  },
  data() {
    return {
      formData: {
        id: null,
        cover_image: "",
        name: "",
        productCategory: "",
        LastPurchase: "",
      },
      errors: {
        name: "",
        productCategory: "",
        LastPurchase: "",
      },
    };
  },
  watch: {
    rowData: {
      immediate: true,
      handler(newVal) {
        this.formData = { ...newVal }; // Populate formData with rowData for edit and view modes
      },
    },
  },
  methods: {
    save() {
      // Reset errors
      this.errors = {
        name: "",
        productCategory: "",
        LastPurchase: "",
        cover_image: "",
      };

      // Validate inputs
      let isValid = true;

      if (!this.formData.name) {
        this.errors.name = this.$t("NameRequired");
        isValid = false;
      }

      if (!this.formData.productCategory) {
        this.errors.productCategory = this.$t("CategoryRequired");
        isValid = false;
      }

      if (
        this.formData.LastPurchase &&
        isNaN(new Date(this.formData.LastPurchase))
      ) {
        this.errors.LastPurchase = this.$t("InvalidDate");
        isValid = false;
      }

      // Validate image presence for add and edit modes
      if (
        (this.mode === "add" || this.mode === "edit") &&
        !this.formData.cover_image
      ) {
        this.errors.cover_image = this.$t("ImageRequired");
        isValid = false;
      }

      if (!isValid) {
        this.toast.error(this.$t("FormErrors"));
        return;
      }

      // Format date if valid
      if (this.formData.LastPurchase) {
        this.formData.LastPurchase = new Date(this.formData.LastPurchase)
          .toISOString()
          .split("T")[0];
      }

      this.$emit("save", this.formData);
      this.toast.success(this.$t("ProductSaved"));
    },
    deleteRow() {
      this.$emit("save", this.rowData);
      this.toast.success(this.$t("ProductDeleted"));
    },
    handleImageUpload(event) {
      const file = event.target.files[0];
      this.processFile(file);
    },
    handleDrop(event) {
      event.preventDefault();
      const file = event.dataTransfer.files[0];
      this.processFile(file);
    },
    processFile(file) {
      if (file) {
        // Validate file type
        const validTypes = [
          "image/jpeg",
          "image/jpg",
          "image/png",
          "image/gif",
        ];
        if (!validTypes.includes(file.type)) {
          this.toast.error(this.$t("InvalidImageType"));
          return;
        }
        // Validate file size (max 2MB)
        const maxSize = 2 * 1024 * 1024; // 2MB
        if (file.size > maxSize) {
          this.toast.error(this.$t("ImageTooLarge"));
          return;
        }
        const reader = new FileReader();
        reader.onload = (e) => {
          const img = new Image();
          img.onload = () => {
            // Validate image dimensions (max width: 2000px, max height: 2000px)
            const maxWidth = 2000;
            const maxHeight = 2000;
            if (img.width > maxWidth || img.height > maxHeight) {
              this.toast.error(this.$t("ImageDimensionsTooLarge"));
              return;
            }
            this.formData.cover_image = e.target.result;
          };
          img.onerror = () => {
            this.toast.error(this.$t("InvalidImageFile"));
          };
          img.src = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
  },
};
</script>

<style scoped>
.popup-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: #ffffff;
  border-radius: 16px;
  width: 610px;
  max-width: 90%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-button {
  background: none;
  border: none;
  color: #292d32;
  font-family: Almarai !important;
  font-size: 25px;
  text-align: center;
  cursor: pointer;
  transition: color 0.3s;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 100%;
  max-width: 559px;
  height: 58px;
  padding: 8px 16px;
  border-radius: 40px;
  border: none;

  font-size: 14px;
  cursor: pointer;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .popup-actions button {
    height: 48px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .popup-actions button {
    height: 42px;
    padding: 6px 12px;
  }
}

.popup-actions button:first-child {
  background-color: var(--primary-color);
  color: #ffffff;
  font-family: Almarai !important;
}

.popup-actions button:last-child {
  background-color: #ffffff;
  border: 1px solid #111111;
  font-family: Almarai !important;
  color: #545454;
}

form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
select {
  width: 99%;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
.input-container.file {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: auto;
}
.upload-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
  border: 2px dashed #ddd;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 10px;
}
.upload-icon {
  font-size: 2rem;
  color: #999;
}
.upload-text {
  text-align: center;
  color: #999;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
}
.preview-image {
  max-width: 25%;
  text-align: center;
  height: auto;
  border-radius: 8px;
}
.load {
  width: 138px;
  height: 32px;
  border-radius: 20px;
  gap: 5px;
  background-color: var(--primary-color);
  color: #ffffff;
  outline: none;
  padding: 5px 10px;
  margin-top: 15px;
}
</style>
