<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="boxDetails">
        <boxDetails />
      </div>
      <LoadingHere :isLoading="isLoading" />
      <div class="table-container">
        <div class="headerTable">
          <div class="icon_text">
            <div class="icon">
              <SvgIcon name="resturant"></SvgIcon>
            </div>
            <p>{{ $t("restuarants") }}</p>
          </div>
          <div class="search_add">
            <div class="search">
              <input type="text" v-model="filter" :placeholder="$t('search')" />
              <SvgIcon class="svg" name="search"></SvgIcon>
            </div>
            <button class="search-button" @click="openPopup('add', {})">
              <p class="addRestbtn">{{ $t("AddRest") }}</p>
              <span class="icon">
                <SvgIcon name="add"></SvgIcon>
              </span>
            </button>
          </div>
        </div>
        <div class="table-responsive">
          <table>
            <thead>
              <tr class="table-header">
                <th>
                  <div
                    style="
                      display: flex;
                      align-items: center;
                      margin-right: 10px;
                      gap: 5px;
                    "
                  >
                    <SvgIcon name="circle"></SvgIcon>

                    {{ $t("headTable1") }}
                  </div>
                </th>
                <th>{{ $t("headTable2") }}</th>
                <th>{{ $t("headTable3") }}</th>
                <th>{{ $t("headTable4") }}</th>
                <th>{{ $t("headTable5") }}</th>
                <th>{{ $t("AllProcedures") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(rows, index) in filteredRows"
                :key="`product-${index}`"
              >
                <td
                  style="
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                    gap: 5px;
                  "
                >
                  <SvgIcon name="dot_circle"></SvgIcon>

                  {{ rows.id }}
                </td>
                <td>{{ rows.restName }}</td>
                <td>{{ rows.productCount }}</td>
                <td>{{ rows.orderCount }}</td>
                <td>{{ rows.lastPurchase }}</td>
                <td class="actions">
                  <div class="iconsIctions">
                    <div class="icon" @click="openPopup('edit', rows)">
                      <SvgIcon class="svg" name="edit"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('view', rows)">
                      <SvgIcon class="svg" name="view"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('delete', rows)">
                      <SvgIcon class="svg delete" name="deleteItem"></SvgIcon>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <dashboardPopup
      v-if="isPopupVisible"
      :type="popupType"
      :row="selectedRow"
      @close="closePopup"
      @save="handlePopupSave"
    />
  </div>
</template>

<script>
import boxDetails from "@/components/dashboard/boxDetails.vue";
import DashboardPopup from "@/components/dashboard/popus/dashboardPopup.vue";
import axios from "axios";
import SvgIcon from "@/components/SvgIcon.vue";
import LoadingHere from "@/components/LoadingHere.vue"; // Import LoadingHere component

export default {
  name: "RestaurantManagement",
  components: {
    boxDetails,
    DashboardPopup,
    LoadingHere,
    SvgIcon,
  },
  data() {
    return {
      filter: "",
      rows: [
        // More products here
      ],
      isPopupVisible: false,
      popupType: "",
      selectedRow: {},
            isLoading: false, // Add loading state

    };
  },

  mounted() {
    this.isLoading = true;
    this.FetchRows(); // Fetch rows dynamically

    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          (typeof row.restName === "string" &&
            row.restName.toLowerCase().includes(searchTerm)) ||
          (row.id && row.id.toString().toLowerCase().includes(searchTerm))
        );
      });
    },
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
    FetchRows() {
      const token = localStorage.getItem("authToken");
      axios
        .get(
          "https://backend.alaqdk.com/api/admin/dashboard/get-approved-restaurants",
          {
            headers: {
              "Accept-Language": "ar",
              Authorization: `Bearer ${token}`, // Add the Authorization header
            },
          }
        )
        .then((response) => {
          this.isLoading = false;
          console.log("Restaurant Response:", response.data); // Log the response
          const data = response.data;
          if (data.status) {
            this.rows = data.data.map((item) => ({
              id: item.restaurant.id,
              restName: item.restaurant.name || "N/A",
              productCount: item.total_products || 0,
              orderCount: item.total_orders || 0,
              lastPurchase: item.last_order || "No orders yet",
            }));
            console.log(this.rows);
          } else {
            console.error("Failed to fetch rows data:", data.message);
          }
        })
        .catch((error) => {
          this.isLoading = false;
          console.error("Error fetching rows data:", error);
        });
    },
    openPopup(type, row) {
      console.log(`Opening popup of type: ${type}`, row); // Debugging log
      this.popupType = type;
      this.selectedRow = { ...row };
      this.isPopupVisible = true;
    },
    closePopup() {
      this.isPopupVisible = false;
    },
    handlePopupSave(updatedRow) {
      if (this.popupType === "edit") {
        const index = this.rows.findIndex((row) => row.id === updatedRow.id);
        if (index !== -1) {
          this.$set(this.rows, index, updatedRow); // Use Vue's reactivity system
        }
      } else if (this.popupType === "delete") {
        this.rows = this.rows.filter((row) => row.id !== updatedRow.id);
      } else if (this.popupType === "add") {
        const nextId =
          this.rows.length > 0
            ? Math.max(...this.rows.map((row) => row.id)) + 1
            : 1;
        updatedRow.id = nextId;
        this.rows.push(updatedRow);
      }
      this.closePopup();
    },
  },
};
</script>
<style lang="scss" scoped>
.Control_panel {
    @media (min-width: 1199px) {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
}

.boxDetails {
  display: flex;

  justify-content: space-evenly;
  gap: 20px;
  margin-left: 30px;
  margin-right: 10px;
}

.table-container {
  margin-top: 20px;
  height: fit-content;
  border-radius: 8px 8px 20px 20px;
  padding: 16px 24px 16px 24px;
  background-color: #ffffff;
  margin-left: 27px;
  thead {
    width: 1, 098px;
    height: 46px;
    border-bottom: 1px;

    background-color: #5c0e350a;
    border: 1px solid #e2e0e0;
    padding-left: 8px;
  }
  thead th {
    width: 121px;
    height: 46px;
    gap: 4px;
  }

  .headerTable {
    width: 1, 098px;
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    .icon_text {
      width: 50%;
      display: flex;
      justify-content: start;
      align-items: center;
      p {
        font-size: 20px;
        line-height: 16px;
        font-weight: 400;
        text-align: right;
        color: #000000;
      }
    }
    .search_add {
      width: 50%;
      display: flex;
      justify-content: end;
      align-items: center;
      .search {
        position: relative;
        border-radius: 24px;
        padding: 0 16px 0 16px;
        gap: 4px;
        border: 1px solid #e2e0e0;
        background-color: #ffffff;
        margin-left: 20px;
        input {
          height: 45px;
          border: none;
          outline: none;
          padding-right: 20px;
          margin-left: 20px;
        }
        .svg {
          position: absolute;
          right: 10px;
          top: 12px;
        }
      }
      .search-button {
        width: 151px;
        height: 45px;
        border-radius: 40px;
        background-color: var(--primary-color);
        border: none;

        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        cursor: pointer;
        .addRestbtn {
          width: 109px;
          height: 21px;
          font-size: 14px;
          font-weight: 400;
          text-align: center;
          margin-top: 18px;
        }
        .icon {
          width: 20px;
          height: 20px;
          display: center;
          align-items: center;
        }
      }
    }
    .icon {
      width: 44px;
      height: 44px;
      border-radius: 120px;
      gap: 10px;
      background-color: #5c0e3514;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 10px;
      i {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border-style: stroke;
      }
    }
  }
  p {
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 700;
  }

  .table-responsive {
    overflow-x: auto;
    border-radius: 8px;
    background-color: #fff;

    table {
      width: 100%;
      border-collapse: collapse;
      border: none th, td {

      }

      th {
        text-align: left;

        /* Alignment for RTL */
        [dir="rtl"] & {
          text-align: right;
        }
        color: black;
      }
      tbody tr {
        width: 1, 098px;
        height: 56px;
      }
      td {
        width: 121px;
        height: 54px;
        button {
          margin: 0 5px;
          cursor: pointer;
        }
      }
    }
  }
}
.actions {
  display: flex !important;
  justify-content: start;
  gap: 10px;
  .iconsIctions {
    width: 122.5px;
    height: 44.17px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .icon {
      width: 44.17px;
      height: 44.17px;

      gap: 10px;
    }
    .icon .svg {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 10px;
      height: 10px;
      background-color: #5c0e350a;
      padding: 10px;
      border-radius: 50%;
    }
    .icon .svg.delete {
      background-color: #ff00002f;
    }
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
      .Control_panel {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
 

  .boxDetails {
    gap: 10px;
  }
}
@media (max-width: 992px) {
  thead th {
    font-size: 14px !important;
  }
}

@media (max-width: 992px) and (min-width: 1199px) {
  .table-container {
    width: 200px !important;
  }
  .search_add {
    .search {
      width: 150px;

      input {
        width: 100px !important;
      }
    }
  }
}
@media (max-width: 768px) and (min-width: 991px) {
  .Control_panel {
    margin: 0 !important;
  }
  .table-container {
    width: 0 !important;
    p {
      font-size: 14px !important;
    }
    .search_add {
      .search {
        width: 200px !important;
      }
    }
    .icon_text p {
      font-size: 14px !important;
    }
    .table-responsive {
      table {
        th,
        td {
          font-size: 12px;
        }
      }
    }
  }
}
/* Media Queries for Small Screens (below 768px) */
@media (max-width: 768px) {
  .Control_panel {
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding: 0 10px;


  }

  .boxDetails {
    flex-direction: column;
    align-items: center !important;
    margin: 5px !important;
    gap: 10px !important;
  }

  .dashboard {
    flex-direction: column;
    margin: 0 !important;
    height: auto !important;

    .summary-container,
    .chart-container {
      width: 90% !important;
      margin: 5px 0 !important;
      height: auto;
      padding: 15px;

      #orders-chart,
      #sales-purchases-chart {
        width: 100% !important;
        height: 200px !important;
      }
    }
  }

  .table-container {
    margin: 0 !important;
    padding: 10px !important;
    overflow-x: auto;

    .headerTable {
      flex-direction: column;
      height: auto;
      gap: 15px;

      .icon_text,
      .search_add {
        width: 100% !important;
        justify-content: center !important;
        flex-direction: column;
      }

      .search {
        input {
        }
      }

      .search-button {
        width: 100% !important;
        margin-top: 10px;
      }
    }

    .table-responsive {
      overflow-x: auto;

      table {
        min-width: 600px;

        th,
        td {
          padding: 8px 4px;
          font-size: 12px;
        }
      }
    }
  }
}
</style>
