<template>
  <div>
    <div v-if="questions.length === 0" class="empty-state">
      {{ $t("noQuestions") }}
    </div>
    <div class="card" v-for="question in questions" :key="question.id">
      <div class="header">
        <h3 class="title">{{ $t(question.question) }}</h3>
        <div class="actions">
          <button
            class="action-btn"
            @click="editQuestion(question.id)"
            :aria-label="$t('edit')"
          >
            {{ $t("edit") }}
            <font-awesome-icon :icon="['fas', 'pen-to-square']" />
          </button>
          <button
            class="action-btn delete"
            @click="deleteQuestion(question.id)"
            :aria-label="$t('delete')"
          >
            {{ $t("delete") }}
            <font-awesome-icon :icon="['fas', 'trash']" />
          </button>
        </div>
      </div>
      <p class="description">
        {{ $t(question.answer) }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "helpComponent",
  props: {
    questions: {
      type: Array,
      required: true,
    },
  },
  methods: {
    editQuestion(id) {
      this.$emit("edit-question", id);
    },
    deleteQuestion(id) {
      this.$emit("delete-question", id);
    },
  },
};
</script>

<style scoped>
.card {
  border-radius: 30px;
  background-color: #ffffff;
  padding: 20px;
  margin: 20px 20px 0 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-size: 16px;
  font-weight: bold;
  color: #545454;
}

.actions {
  display: flex;
  gap: 8px;
}

.action-btn {
  background-color: transparent;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  cursor: pointer;
  color: #8a8a8a;
}
.empty-state {
  color: red;
  width: 100%;
  text-align: center;
}

.description {
  margin-top: 12px;
  font-size: 14px;
  color: #8a8a8a;
}
</style>
