<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="boxDetails">
        <boxDetails />
      </div>
      <LoadingHere :isLoading="isLoading" />
      <div class="table-container">
        <div class="headerTable">
          <div class="icon_text">
            <div class="icon">
              <SvgIcon name="productIcon"></SvgIcon>
            </div>
            <p>{{ $t("AllProducts") }}</p>
          </div>
          <div class="search_add">
            <div class="search">
              <input type="text" v-model="filter" :placeholder="$t('search')" />
              <SvgIcon class="svg" name="search"></SvgIcon>
            </div>
            <button class="search-button" @click="openPopup('add')">
              <p class="addRestbtn">{{ $t("AddProd") }}</p>
              <span class="icon">
                <SvgIcon name="add"></SvgIcon>
              </span>
            </button>
          </div>
        </div>
        <div class="table-responsive">
          <table>
            <thead>
              <tr class="table-header">
                <th>
                  <div
                    style="
                      display: flex;
                      align-items: center;
                      margin-right: 10px;
                      gap: 5px;
                    "
                  >
                    <SvgIcon name="circle"></SvgIcon>

                    {{ $t("headTable1") }}
                  </div>
                </th>
                <th>{{ $t("head3Table3") }}</th>
                <th>{{ $t("head3Table4") }}</th>
                <th>{{ $t("ItemCategory") }}</th>
                <!-- Add ItemCategory column -->
                <th>{{ $t("headTable5") }}</th>
                <th>{{ $t("AllProcedures") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(rows, index) in filteredRows"
                :key="`product-${index}`"
              >
                <td
                  style="
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                    gap: 5px;
                  "
                >
                  <SvgIcon name="dot_circle"></SvgIcon>
                  {{ rows.id }}
                </td>
                <td>
                  <img
                    :src="rows.cover_image"
                    alt="Product Image"
                    @error="handleImageError($event)"
                    style="width: 50px; height: 50px; object-fit: cover"
                  />
                </td>
                <td>{{ rows.name }}</td>
                <td>{{ rows.productCategory }}</td>
                <!-- Display ItemCategory -->
                <td>{{ rows.LastPurchase }}</td>
                <td class="actions">
                  <div class="iconsIctions">
                    <div class="icon" @click="openPopup('edit', rows)">
                      <SvgIcon class="svg" name="edit"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('view', rows)">
                      <SvgIcon class="svg" name="view"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('delete', rows)">
                      <SvgIcon class="svg delete" name="deleteItem"></SvgIcon>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <product-popup
    v-if="isPopupVisible"
    :mode="popupMode"
    :row-data="selectedRow"
    @close="isPopupVisible = false"
    @save="handleSave"
  />
</template>

<script>
// @ is an alias to /src
import boxDetails from "@/components/dashboard/boxDetails.vue";
import productPopup from "@/components/dashboard/popus/productPopup.vue";
import axios from "axios";
import SvgIcon from "@/components/SvgIcon.vue";
import LoadingHere from "@/components/LoadingHere.vue"; // Import LoadingHere component

export default {
  name: "ProductManagement",
  components: {
    boxDetails,
    productPopup,
    SvgIcon,
    LoadingHere,
  },
  data() {
    return {
      filter: "",

      rows: [
        // More products here
      ],
      isPopupVisible: false,
      popupMode: "",
      selectedRow: null,
      isLoading: false, // Add loading state
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          (row.name && row.name.toLowerCase().includes(searchTerm)) || // Filter by name
          (row.productCategory &&
            row.productCategory.toLowerCase().includes(searchTerm)) // Filter by category
        );
      });
    },
  },
  mounted() {
    this.isLoading = true; // Set loading state to true
    this.FetchRows(); // Fetch rows dynamically
    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
    FetchRows() {
      const token = localStorage.getItem("authToken");
      axios
        .get(
          "https://backend.alaqdk.com/api/admin/dashboard/get-all-products",
          {
            headers: {
              "Accept-Language": "ar",
              Authorization: `Bearer ${token}`, // Add the Authorization header
            },
          }
        )
        .then((response) => {
          this.isLoading = false;

          console.log("product Response Data:", response.data); // Debug the API response
          const data = response.data;
          if (data.status) {
            this.rows = data.data.map((product) => ({
              id: product.id,
              cover_image: product.cover_image || "N/A",
              name: product.name || "N/A",
              productCategory: product.productCategory?.name || "N/A",
              LastPurchase: product.updated_at || "N/A", // Ensure this matches the table column
            }));
            console.log("Processed Rows:", this.rows); // Debug the processed rows
          } else {
            console.error("Failed to fetch rows data:", data.message);
          }
        })
        .catch((error) => {
          this.isLoading = false;

          console.error("Error fetching rows data:", error);
        });
    },
    openPopup(mode, row = null) {
      this.popupMode = mode;
      this.selectedRow = row || {
        id: null,
        cover_image: "",
        name: "",
        productCategory: "",
        LastPurchase: new Date().toISOString().split("T")[0], // Ensure valid date format
      };
      this.isPopupVisible = true;
    },
    handleSave(updatedRow) {
      if (this.popupMode === "add") {
        updatedRow.id =
          this.rows.length > 0
            ? Math.max(...this.rows.map((r) => r.id)) + 1
            : 1;
        this.rows.push(updatedRow);
      } else if (this.popupMode === "edit") {
        const index = this.rows.findIndex((r) => r.id === updatedRow.id);
        if (index !== -1) {
          this.rows.splice(index, 1, updatedRow);
        }
      } else if (this.popupMode === "delete") {
        this.rows = this.rows.filter((r) => r.id !== updatedRow.id);
      }
      this.isPopupVisible = false;
    },
    handleImageError(event) {
      event.target.src = "https://via.placeholder.com/50"; // Placeholder image URL
    },
  },
};
</script>
<style lang="scss" scoped>
.Control_panel {
  @media (min-width: 1199px) {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
  .header {
    display: flex;
    align-content: center;
    padding-top: 40px;
    width: 100%;
  }
  .content p {
    margin: 0;
    width: 200px !important;
    height: 27px;
    font-weight: 400;
    color: #000000;
    margin-top: 20px;
    line-height: 5px;
    font-size: 18px;
  }
}

.boxDetails {
  display: flex;

  justify-content: space-evenly;
  gap: 20px;
  margin-left: 30px;
  margin-right: 10px;
}

.table-container {
  margin-top: 20px;
  height: fit-content;
  border-radius: 8px 8px 20px 20px;
  padding: 16px 24px 16px 24px;
  background-color: #ffffff;
  margin-left: 27px;
  thead {
    width: 1, 098px;
    height: 46px;
    border-bottom: 1px;

    background-color: #5c0e350a;
    border: 1px solid #e2e0e0;
    padding-left: 8px;
  }
  thead th {
    width: 121px;
    height: 46px;
    gap: 4px;
  }

  .headerTable {
    width: 1, 098px;
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    .icon_text {
      width: 50%;
      display: flex;
      justify-content: start;
      align-items: center;
      p {
        font-size: 20px;
        line-height: 16px;
        font-weight: 400;
        text-align: right;
        color: #000000;
      }
    }
    .search_add {
      width: 50%;
      display: flex;
      justify-content: end;
      align-items: center;
      .search {
        position: relative;
        border-radius: 24px;
        padding: 0 16px 0 16px;
        gap: 4px;
        border: 1px solid #e2e0e0;
        background-color: #ffffff;
        margin-left: 20px;
        input {
          height: 45px;
          border: none;
          outline: none;
          padding-right: 20px;
          margin-left: 20px;
        }
        .svg {
          position: absolute;
          right: 10px;
          top: 12px;
        }
      }
      .search-button {
        width: 151px;
        height: 45px;
        border: none;

        border-radius: 40px;
        background-color: var(--primary-color);
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        cursor: pointer;
        .addRestbtn {
          width: 109px;
          height: 21px;
          font-size: 14px;
