          width: 20px;
          height: 20px;
          display: center;
          align-items: center;
        }
      }
    }
    .icon {
      width: 44px;
      height: 44px;
      border-radius: 120px;
      gap: 10px;
      background-color: #5c0e3514;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 10px;
      i {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border-style: stroke;
      }
    }
  }
  p {
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 700;
  }

  .table-responsive {
    overflow-x: auto;
    border-radius: 8px;
    background-color: #fff;

    table {
      width: 100%;
      border-collapse: collapse;
      border: none th, td {

      }

      th {
        text-align: left;

        /* Alignment for RTL */
        [dir="rtl"] & {
          text-align: right;
        }
        color: black;
      }
      tbody tr {
        width: 1, 098px;
        height: 56px;
      }
      td {
        width: 121px;
        height: 54px;
        button {
          margin: 0 5px;
          cursor: pointer;
        }
      }
    }
  }
}
.actions {
  display: flex !important;
  justify-content: start;
  gap: 10px;
  .iconsIctions {
    width: 122.5px;
    height: 44.17px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .icon {
      width: 44.17px;
      height: 44.17px;

      gap: 10px;
    }
    .icon .svg {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 10px;
      height: 10px;
      background-color: #5c0e350a;
      padding: 10px;
      border-radius: 50%;
    }
    .icon .svg.delete {
      background-color: #ff00002f;
    }
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .Control_panel {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }
  .content {
    width: 200px;
    height: 27px;
  }
  .content p {
    font-size: 18px !important;
  }
  .content span {
    font-size: 13px !important;
  }
  .boxDetails {
    gap: 10px;
  }
}
@media (max-width: 992px) {
  thead th {
    font-size: 14px !important;
  }
}

@media (max-width: 992px) and (min-width: 1199px) {
  .table-container {
    width: 200px !important;
  }
  .search_add {
    .search {
      width: 150px;

      input {
        width: 100px !important;
      }
    }
  }
}
@media (max-width: 768px) and (min-width: 991px) {
  .Control_panel {
    margin: 0 !important;
  }
  .table-container {
    width: 0 !important;
    p {
      font-size: 14px !important;
    }
    .search_add {
      .search {
        width: 200px !important;
      }
    }
    .icon_text p {
      font-size: 14px !important;
    }
    .table-responsive {
      table {
        th,
        td {
          font-size: 12px;
        }
      }
    }
  }
}
/* Media Queries for Small Screens (below 768px) */
@media (max-width: 768px) {
  .Control_panel {
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding: 0 10px;
  }

  .boxDetails {
    flex-direction: column;
    align-items: center !important;
    margin: 5px !important;
    gap: 10px !important;
  }

  .dashboard {
    flex-direction: column;
    margin: 0 !important;
    height: auto !important;

    .summary-container,
    .chart-container {
      width: 90% !important;
      margin: 5px 0 !important;
      height: auto;
      padding: 15px;

      #orders-chart,
      #sales-purchases-chart {
        width: 100% !important;
        height: 200px !important;
      }
    }
  }

  .table-container {
    margin: 0 !important;
    padding: 10px !important;
    overflow-x: auto;

    .headerTable {
      flex-direction: column;
      height: auto;
      gap: 15px;

      .icon_text,
      .search_add {
        width: 100% !important;
        justify-content: center !important;
        flex-direction: column;
      }

      .search {
        input {
        }
      }

      .search-button {
        width: 100% !important;
        margin-top: 10px;
      }
    }

    .table-responsive {
      overflow-x: auto;

      table {
        min-width: 600px;

        th,
        td {
          padding: 8px 4px;
          font-size: 12px;
        }
      }
    }
  }
}
</style>
