<template>
  <div class="order-summary">
    <div class="text-container">
      <p class="title">{{ $t("Orders") || "N/A" }}</p>
      <p class="count">{{ dataDashboard.ordersCount || 0 }}</p>
      <!-- Updated -->
    </div>
    <div class="icon-container">
      <SvgIcon name="icon-container"></SvgIcon>
    </div>
  </div>
  <div class="order-summary">
    <div class="text-container">
      <p class="title">{{ $t("CompletedOrders") || "N/A" }}</p>
      <p class="count">{{ dataDashboard.doneOrdersCount || 0 }}</p>
      <!-- Updated -->
    </div>
    <div class="icon-container">
      <SvgIcon name="icon-container1"></SvgIcon>
    </div>
  </div>
  <div class="order-summary">
    <div class="text-container">
      <p class="title">{{ $t("totalClients") || "N/A" }}</p>
      <p class="count">{{ dataDashboard.totalClients || 0 }}</p>
      <!-- Updated -->
    </div>
    <div class="icon-container">
      <SvgIcon name="icon-container2"></SvgIcon>
    </div>
  </div>
  <div class="order-summary">
    <div class="text-container">
      <p class="title">{{ $t("ApprovedRestaurants") || "N/A" }}</p>
      <p class="count">{{ dataDashboard.totalApprovedRestaurants || 0 }}</p>
      <!-- Updated -->
    </div>
    <div class="icon-container">
      <SvgIcon name="icon-container"></SvgIcon>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  name: "OrderSummary",

  data() {
    return {
      dataDashboard: {
        ordersCount: 0,
        doneOrdersCount: 0,
        totalApprovedRestaurants: 0,
        totalClients: 0,
      }, // Initialized properties
    };
  },
  components: {
    SvgIcon,
  },
  mounted() {
    this.fetchDashboardData();
  },
  methods: {
    fetchDashboardData() {
      const token = localStorage.getItem("authToken");
      axios
        .get(
          "https://backend.alaqdk.com/api/admin/dashboard/get-dashboard-data",
          {
            headers: {
              "Accept-Language": "ar",
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then((response) => {
          console.log("API Response:", response.data);
          const data = response.data;
          if (data.status) {
            this.dataDashboard = data.data || {};
            console.log(data.data);
          } else {
            console.error("Failed to fetch dashboard data:", data.message);
          }
        })
        .catch((error) => {
          console.error("Error fetching dashboard data:", error);
        });
    },
  },
};
</script>

<style scoped>
.order-summary {
  width: 268px;
  height: 112px;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px 8px 20px;
  background-color: #ffffff;
  padding: 10px 20px 8px 20px;
  margin-top: 20px;
}

.icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  width: 52px;
  height: 52px;
  border-radius: 120px;
  background-color: rgba(92, 14, 53, 0.08);
}

.icon {
  color: rgba(251, 176, 64, 1);
}

.text-container {
  gap: 8px;
}

.title {
  font-weight: 400;
  font-size: 16px;
  color: #000000;
}

.count {
  margin: 0;
  font-weight: 700;
  font-size: 28px;
  color: var(--primary-color); /*   */
}
@media (min-width: 992px) and (max-width: 1199px) {
  .text-container .title {
    font-size: 13px;
  }
  .text-container .count {
    font-size: 20px;
  }
  .icon-container {
    width: 30px;
    height: 30px;
  }
}
</style>
