<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="table-container">
        <div class="headerTable">
          <div class="icon_text">
            <div class="icon">
              <SvgIcon name="icon-container2"></SvgIcon>
            </div>
            <p>{{ $t("Aside_LI11") }}</p>
          </div>
          <div class="search_add">
            <div class="search">
              <input type="text" v-model="filter" :placeholder="$t('search')" />
              <SvgIcon class="svg" name="search"></SvgIcon>
            </div>
            <button class="search-button" @click="openPopup('add')">
              <p class="addRestbtn">{{ $t("add") }}</p>
              <span class="icon">
                <SvgIcon name="add"></SvgIcon>
              </span>
            </button>
          </div>
        </div>
        <div class="table-responsive">
          <table>
            <thead>
              <tr class="table-header">
                <th>
                  <div
                    style="
                      display: flex;
                      align-items: center;
                      margin-right: 10px;
                      gap: 5px;
                    "
                  >
                    <SvgIcon name="circle"></SvgIcon>

                    {{ $t("headTable1") }}
                  </div>
                </th>
                <th>{{ $t("name") }}</th>
                <th>{{ $t("Phone") }}</th>
                <th>{{ $t("role") }}</th>
                <th>{{ $t("AllProcedures") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(rows, index) in filteredRows"
                :key="`product-${index}`"
              >
                <td
                  style="
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                    gap: 5px;
                  "
                >
                  <SvgIcon name="dot_circle"></SvgIcon>
                  {{ rows.code }}
                </td>
                <td>{{ rows.name }}</td>
                <td>{{ rows.phone }}</td>
                <td>{{ rows.role }}</td>
                <td class="actions">
                  <div class="iconsIctions">
                    <div class="icon" @click="openPopup('edit', rows)">
                      <SvgIcon class="svg" name="edit"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('view', rows)">
                      <SvgIcon class="svg" name="view"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('delete', rows)">
                      <SvgIcon class="svg delete" name="deleteItem"></SvgIcon>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <rolePopup
    v-if="isPopupVisible"
    :type="popupType"
    :data="selectedRow"
    @close="closePopup"
    @save="handleSave"
    @delete="handleDelete"
  />
</template>

<script>
// @ is an alias to /src
import rolePopup from "@/components/dashboard/popus/rolePopup.vue";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  name: "addBanners",
  components: {
    rolePopup,
    SvgIcon,
  },
  data() {
    return {
      filter: "",
      rows: [
        {
          code: 1,
          name: " ",
          phone: "+97450123456",
          role: "  ",
        },
        {
          code: 2,
          name: " ",
          phone: "+97450123456",
          role: "  ",
        },
        // More products here
      ],
      isPopupVisible: false,
      popupType: "",
      selectedRow: null,
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          typeof row.name === "string" &&
          row.name.toLowerCase().includes(searchTerm) // Ensure 'orderNumber' is defined
        );
      });
    },
  },
  mounted() {
    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
    openPopup(type, row = null) {
      this.popupType = type;
      this.selectedRow = row
        ? { ...row } // Clone the row to avoid direct mutation
        : {
            code:
              this.rows.length > 0
                ? Math.max(...this.rows.map((r) => r.code)) + 1
                : 1,
            name: "",
            phone: "",
            role: "",
          };
      this.isPopupVisible = true;
    },
    closePopup() {
      this.isPopupVisible = false;
      this.popupType = "";
      this.selectedRow = null;
    },
    handleSave(updatedRow) {
      if (this.popupType === "add") {
        this.rows.push(updatedRow);
      } else if (this.popupType === "edit") {
        const index = this.rows.findIndex(
          (row) => row.code === updatedRow.code
        );
        if (index !== -1) this.rows.splice(index, 1, updatedRow);
      }
      this.closePopup();
    },
    handleDelete(code) {
      this.rows = this.rows.filter((row) => row.code !== code);
      this.closePopup();
    },
  },
};
</script>
