<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="boxDetails">
        <boxDetails />
      </div>
      <div class="dashboard">
        <div class="summary-container">
          <p>{{ $t("OrderPlanner") }}</p>
          <div class="order-summary">
            <span class="order-count">11,756</span>
            <span class="order-percentage">+23%</span>
          </div>
          <canvas id="orders-chart"></canvas>
          <div class="month-selector">
            <span>Jan</span>
            <span>Feb</span>
            <span class="active">March</span>
            <span>Apr</span>
            <span>May</span>
          </div>
        </div>
        <div class="chart-container">
          <div class="Flex">
            <p>{{ $t("Sales_purchase") }}</p>
            <div class="legend">
              <span><span class="sales-dot"></span> {{ $t("Sales") }}</span>
            </div>
          </div>
          <canvas id="sales-purchases-chart"></canvas>
        </div>
      </div>
      <div class="table-container">
        <div class="headerTable">
          <div class="icon_text">
            <div class="icon">
              <SvgIcon name="resturant"></SvgIcon>
            </div>
            <p>{{ $t("restuarants") }}</p>
          </div>
          <div class="search_add">
            <div class="search">
              <input type="text" v-model="filter" :placeholder="$t('search')" />
              <SvgIcon class="svg" name="search"></SvgIcon>
            </div>
            <button class="search-button" @click="openPopup('add', {})">
              <p class="addRestbtn">{{ $t("AddRest") }}</p>
              <span class="icon">
                <SvgIcon name="add"></SvgIcon>
              </span>
            </button>
          </div>
        </div>
        <LoadingHere :isLoading="isLoading" />

        <div class="table-responsive">
          <table>
            <thead>
              <tr class="table-header">
                <th>
                  <div
                    style="
                      display: flex;
                      align-items: center;
                      margin-right: 10px;
                      gap: 5px;
                    "
                  >
                    <SvgIcon name="circle"></SvgIcon>

                    {{ $t("headTable1") }}
                  </div>
                </th>
                <th>{{ $t("headTable2") }}</th>
                <th>{{ $t("headTable3") }}</th>
                <th>{{ $t("headTable4") }}</th>
                <th>{{ $t("headTable5") }}</th>
                <th>{{ $t("AllProcedures") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(rows, index) in filteredRows"
                :key="`product-${index}`"
              >
                <td
                  style="
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                    gap: 5px;
                  "
                >
                  <SvgIcon name="dot_circle"></SvgIcon>
                  {{ rows.id }}
                </td>
                <td>{{ rows.restName }}</td>
                <td>{{ rows.productCount }}</td>
                <td>{{ rows.orderCount }}</td>
                <td>{{ rows.lastPurchase }}</td>
                <td class="actions">
                  <div class="iconsIctions">
                    <div class="icon" @click="openPopup('edit', rows)">
                      <SvgIcon class="svg" name="edit"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('view', rows)">
                      <SvgIcon class="svg" name="view"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('delete', rows)">
                      <SvgIcon class="svg delete" name="deleteItem"></SvgIcon>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <dashboardPopup
    v-if="isPopupVisible"
    :type="popupType"
    :row="selectedRow"
    @close="closePopup"
    @save="handlePopupSave"
  />
</template>

<script>
// @ is an alias to /src
import boxDetails from "@/components/dashboard/boxDetails.vue";
import { Chart, registerables } from "chart.js";
import DashboardPopup from "@/components/dashboard/popus/dashboardPopup.vue";
import LoadingHere from "@/components/LoadingHere.vue";
Chart.register(...registerables);
import axios from "axios";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  name: "dashboardHome",
  components: {
    boxDetails,
    LoadingHere,
    DashboardPopup,
    SvgIcon,
  },

  data() {
    return {
      filter: "",
      rows: [
        // More products here
      ],
      isPopupVisible: false,
      popupType: "",
      selectedRow: {},
      isLoading: false,
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          (typeof row.restName === "string" &&
            row.restName.toLowerCase().includes(searchTerm)) ||
          (row.id && row.id.toString().toLowerCase().includes(searchTerm))
        );
      });
    },
  },
  mounted() {
    this.isLoading = true;
    this.fetchUserData(); // Fetch user data dynamically
    this.FetchRows(); // Fetch rows dynamically

    this.renderSalesPurchasesChart();
    this.renderOrdersChart();
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
    FetchRows() {
      const token = localStorage.getItem("authToken");
      axios
        .get(
          "https://backend.alaqdk.com/api/admin/dashboard/get-approved-restaurants",
          {
            headers: {
              "Accept-Language": "ar",
              Authorization: `Bearer ${token}`, // Add the Authorization header
            },
          }
        )
        .then((response) => {
          this.isLoading = false;
          console.log("Restaurant Response:", response.data); // Log the response
          const data = response.data;
          if (data.status) {
            this.rows = data.data.map((item) => ({
              id: item.restaurant.id,
              restName: item.restaurant.name || "N/A",
              productCount: item.total_products || 0,
              orderCount: item.total_orders || 0,
              lastPurchase: item.last_order || "No orders yet",
            }));
            console.log(this.rows);
          } else {
            console.error("Failed to fetch rows data:", data.message);
          }
        })
        .catch((error) => {
          this.isLoading = false; // Set loading state to false
          console.error("Error fetching rows data:", error);
        });
    },
    renderSalesPurchasesChart() {
      const ctx = document
        .getElementById("sales-purchases-chart")
        .getContext("2d");
      new Chart(ctx, {
        type: "line",
        data: {
          labels: [
            "1 Oct",
            "3 Oct",
            "7 Oct",
            "10 Oct",
            "14 Oct",
            "20 Oct",
            "23 Oct",
            "27 Oct",
            "30 Oct",
          ],
          datasets: [
            {
              label: "",
              data: [1200, 2000, 1800, 1300, 3000, 2500, 180, 1200, 3000], // Sample data
              borderColor: "#800080",
              borderWidth: 2,
              fill: false,
              tension: 0.4,
              pointRadius: 5,
              pointBackgroundColor: "#800080",
            },
            {
              label: "",
              data: [200, 1000, 1200, 900, 2500, 2800, 180, 1500, 1800], // Sample data
              borderColor: "#D87093",
              borderWidth: 2,
              fill: false,
              tension: 0.4,
              pointRadius: 5,
              pointBackgroundColor: "#D87093",
            },
          ],
        },
        options: {
          scales: {
            y: {
              beginAtZero: true,
              max: 3000, // Set the maximum value
            },
          },
          plugins: {
            legend: {
              display: false, // Hide the legend, use custom legend
            },
          },
        },
      });
    },
    renderOrdersChart() {
      const ctx = document.getElementById("orders-chart").getContext("2d");
      new Chart(ctx, {
        type: "line",
        data: {
          labels: ["Jan", "Feb", "March", "Apr", "May"],
          datasets: [
            {
              label: "Orders",
              data: [10000, 11000, 11756, 10500, 11200], // sample data
              borderColor: "#D87093",
              borderWidth: 3,
              fill: true,
              backgroundColor: "rgba(216, 112, 147, 0.2)",
              tension: 0.4,
              pointRadius: 5,
              pointBackgroundColor: "#800080",
            },
          ],
        },
        options: {
          scales: {
            y: {
              beginAtZero: true,
            },
          },
          plugins: {
            legend: {
              display: false,
            },
          },
        },
      });
    },
    openPopup(type, row) {
      this.popupType = type;
      this.selectedRow = { ...row };
      this.isPopupVisible = true;
    },
    closePopup() {
      this.isPopupVisible = false;
      this.popupType = ""; // Reset popup type
      this.selectedRow = null; // Reset selected row
    },
    handlePopupSave(updatedRow) {
      this.saveChanges(updatedRow); // Call saveChanges when popup emits save
    },
    saveChanges(updatedRow) {
      if (this.popupType === "add") {
        updatedRow.id = Date.now(); // Ensure a unique ID for new rows
        this.rows.push(updatedRow);
      } else if (this.popupType === "edit") {
        const index = this.rows.findIndex((row) => row.id === updatedRow.id);
        if (index !== -1) {
          this.rows.splice(index, 1, updatedRow);
        }
      } else if (this.popupType === "delete") {
        this.rows = this.rows.filter((row) => row.id !== updatedRow.id);
      }
      this.closePopup();
    },
  },
};
</script>
<style lang="scss" scoped>
