        }
      }
      .search-button {
        width: 151px;
        height: 45px;
        border-radius: 40px;
        background-color: var(--primary-color);
        border: none;

        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        cursor: pointer;
        .addRestbtn {
          width: 109px;
          height: 21px;
          font-size: 14px;
          font-weight: 400;
          text-align: center;
          margin-top: 18px;
        }
        .icon {
          width: 20px;
          height: 20px;
          display: center;
          align-items: center;
        }
      }
    }
    .icon {
      width: 44px;
      height: 44px;
      border-radius: 120px;
      gap: 10px;
      background-color: #5c0e3514;
      display: flex;
