    font-size: 20px;
    font-weight: 700;
  }

  .table-responsive {
    overflow-x: auto;
    border-radius: 8px;
    background-color: #fff;

    table {
      width: 100%;
      border-collapse: collapse;
      border: none th, td {

      }

      th {
