<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <div class="Control_panel">
      <div class="table-container">
        <div class="headerTable">
          <div class="icon_text">
            <div class="icon">
              <SvgIcon name="banner"></SvgIcon>
            </div>
            <p>{{ $t("AllBanners") }}</p>
          </div>
          <div class="search_add">
            <div class="search">
              <input type="text" v-model="filter" :placeholder="$t('search')" />
              <SvgIcon class="svg" name="search"></SvgIcon>
            </div>
            <button class="search-button" @click="openPopup('add', {})">
              <p class="addRestbtn">{{ $t("Addbann") }}</p>
              <span class="icon">
                <SvgIcon name="add"></SvgIcon>
              </span>
            </button>
          </div>
        </div>
        <div class="table-responsive">
          <table>
            <thead>
              <tr class="table-header">
                <th>
                  <div
                    style="
                      display: flex;
                      align-items: center;
                      margin-right: 10px;
                      gap: 5px;
                    "
                  >
                    <SvgIcon name="circle"></SvgIcon>

                    {{ $t("headTable1") }}
                  </div>
                </th>
                <th>{{ $t("head8Table2") }}</th>
                <th>{{ $t("head8Table3") }}</th>
                <th>{{ $t("head4Table7") }}</th>
                <th>{{ $t("head8Table5") }}</th>
                <th>{{ $t("AllProcedures") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(rows, index) in filteredRows"
                :key="`product-${index}`"
              >
                <td
                  style="
                    display: flex;
                    align-items: center;
                    margin-right: 10px;
                    gap: 5px;
                  "
                >
                  <SvgIcon name="dot_circle"></SvgIcon>

                  {{ rows.code }}
                </td>
                <td>{{ rows.photo }}</td>
                <td>{{ rows.bannerName }}</td>
                <td>
                  <label class="switch">
                    <input
                      type="checkbox"
                      v-model="rows.state"
                      @change="updateStatus(rows)"
                    />
                    <span class="slider round"></span>
                  </label>
                </td>
                <td>
                  <label class="switch">
                    <input
                      type="checkbox"
                      v-model="rows.Distinctive"
                      @change="updateStatus(rows)"
                    />
                    <span class="slider round"></span>
                  </label>
                </td>
                <td class="actions">
                  <div class="iconsIctions">
                    <div class="icon" @click="openPopup('edit', rows)">
                      <SvgIcon class="svg" name="edit"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('view', rows)">
                      <SvgIcon class="svg" name="view"></SvgIcon>
                    </div>
                    <div class="icon" @click="openPopup('delete', rows)">
                      <SvgIcon class="svg delete" name="deleteItem"></SvgIcon>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <popup-component
    v-if="popupData.visible"
    :type="popupData.type"
    :data="popupData.row"
    @close="closePopup"
    @save="handleSave"
    @delete="confirmDelete"
  />
</template>

<script>
// @ is an alias to /src
import PopupComponent from "@/components/dashboard/popus/bannersPopus.vue";
import SvgIcon from "@/components/SvgIcon.vue";

export default {
  name: "addBanners",
  components: {
    PopupComponent,
    SvgIcon,
  },
  data() {
    return {
      filter: "",
      rows: [
        {
          code: 2,
          photo: "",
          bannerName: " ",
          state: true,
          Distinctive: true,
        },
        // More products here
      ],
      popupData: {
        visible: false,
        type: "",
        row: null,
      },
      deletePopup: {
        visible: false,
        row: null,
      },
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          (typeof row.bannerName === "string" &&
            row.bannerName.toLowerCase().includes(searchTerm)) || // Ensure 'bannerName' is a string
          (row.Distinctive &&
            row.Distinctive.toString().toLowerCase().includes(searchTerm)) // Ensure 'Distinctive' exists
        );
      });
    },
  },
  mounted() {
    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
    updateStatus(rows) {
      // Here you would typically make an API call to update the status on the server
      console.log("Updating status for row:", rows);
      // Example:  axios.put(`/api/items/${rows.code}`, { state: rows.state, Distinctive: rows.Distinctive });
    },
    openPopup(type, row) {
      this.popupData = {
        visible: true,
        type,
        row,
      };
    },
    closePopup() {
      this.popupData.visible = false;
    },
    handleSave(updatedRow) {
      if (this.popupData.type === "edit") {
        const index = this.rows.findIndex(
          (row) => row.code === updatedRow.code
        );
        if (index !== -1) {
          this.rows.splice(index, 1, updatedRow);
        }
      } else if (this.popupData.type === "add") {
        updatedRow.code =
          this.rows.length > 0
            ? Math.max(...this.rows.map((row) => row.code)) + 1
            : 1; // Increment code or start from 1 if no rows exist
        this.rows.push(updatedRow);
      }
      this.closePopup();
    },
    deleteRow(row) {
      const index = this.rows.findIndex((r) => r.code === row.code);
      if (index !== -1) {
        this.rows.splice(index, 1);
      }
    },
    confirmDelete(row) {
      const index = this.rows.findIndex((r) => r.code === row.code);
      if (index !== -1) {
        this.rows.splice(index, 1);
      }
      this.closePopup();
    },
  },
};
</script>
<style lang="scss" scoped>
.Control_panel {
    @media (min-width: 1199px) {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }

}

