  margin-left: 27px;
  thead {
    width: 1, 098px;
    height: 46px;
    border-bottom: 1px;

    background-color: #5c0e350a;
    border: 1px solid #e2e0e0;
    padding-left: 8px;
  }
  thead th {
    width: 121px;
    height: 46px;
    gap: 4px;
  }

  .headerTable {
    width: 1, 098px;
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    .icon_text {
      width: 50%;
      display: flex;
      justify-content: start;
      align-items: center;
      p {
        font-size: 20px;
        line-height: 16px;
        font-weight: 400;
        text-align: right;
        color: #000000;
      }
    }
    .search_add {
      width: 50%;
      display: flex;
      justify-content: end;
      align-items: center;
      .search {
        position: relative;
        border-radius: 24px;
        padding: 0 16px 0 16px;
        gap: 4px;
        border: 1px solid #e2e0e0;
        background-color: #ffffff;
        margin-left: 20px;
        input {
          height: 45px;
          border: none;
          outline: none;
          padding-right: 20px;
          margin-left: 20px;
        }
        .svg {
          position: absolute;
          right: 10px;
          top: 12px;
        }
      }
      .search-button {
        width: 151px;
        height: 45px;
        border-radius: 40px;
        background-color: var(--primary-color);
        display: flex;
        border: none;

        justify-content: center;
        align-items: center;
        color: white;
        cursor: pointer;
        border: none;
        .addRestbtn {
          width: 109px;
          height: 21px;
          font-size: 14px;
          font-weight: 400;
          text-align: center;
          margin-top: 18px;
        }
        .icon {
          width: 20px;
          height: 20px;
          display: center;
          align-items: center;
        }
      }
    }
    .icon {
      width: 44px;
      height: 44px;
      border-radius: 120px;
      gap: 10px;
      background-color: #5c0e3514;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 10px;
      i {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border-style: stroke;
      }
    }
  }
  p {
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 700;
  }

  .table-responsive {
    overflow-x: auto;
    border-radius: 8px;
    background-color: #fff;

    table {
      width: 100%;
      border-collapse: collapse;
      border: none th, td {

      }

      th {
        text-align: left;

        /* Alignment for RTL */
        [dir="rtl"] & {
          text-align: right;
        }
        color: black;
      }
      tbody tr {
        width: 1, 098px;
        height: 56px;
      }
      td {
        width: 121px;
        height: 54px;
        button {
          margin: 0 5px;
          cursor: pointer;
        }
      }
    }
  }
}
.actions {
  display: flex !important;
  justify-content: start;
  gap: 10px;
  .iconsIctions {
    width: 122.5px;
    height: 44.17px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .icon {
      width: 44.17px;
      height: 44.17px;

      gap: 10px;
    }
    .icon .svg {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 10px;
      height: 10px;
      background-color: #5c0e350a;
      padding: 10px;
      border-radius: 50%;
    }
    .icon .svg.delete {
      background-color: #ff00002f;
    }
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
      .Control_panel {
    margin-left: 18rem;

    /*    */
    [dir="rtl"] & {
      margin-left: 0;
      margin-right: 20rem;
    }
  }

  .boxDetails {
    gap: 10px;
  }
}
@media (max-width: 992px) {
  thead th {
    font-size: 14px !important;
  }
}

@media (max-width: 992px) and (min-width: 1199px) {
  .table-container {
    width: 200px !important;
  }
  .search_add {
    .search {
      width: 150px;

      input {
        width: 100px !important;
      }
    }
  }
}
@media (max-width: 768px) and (min-width: 991px) {
  .Control_panel {
    margin: 0 !important;
  }
  .table-container {
    width: 0 !important;
    p {
      font-size: 14px !important;
    }
    .search_add {
      .search {
        width: 200px !important;
      }
    }
    .icon_text p {
      font-size: 14px !important;
    }
    .table-responsive {
      table {
        th,
        td {
          font-size: 12px;
        }
      }
    }
  }
}
/* Media Queries for Small Screens (below 768px) */
@media (max-width: 768px) {
  .Control_panel {
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding: 0 10px;

  }

  .boxDetails {
    flex-direction: column;
    align-items: center !important;
    margin: 5px !important;
    gap: 10px !important;
  }

  .dashboard {
    flex-direction: column;
    margin: 0 !important;
    height: auto !important;

    .summary-container,
    .chart-container {
      width: 90% !important;
      margin: 5px 0 !important;
      height: auto;
      padding: 15px;

      #orders-chart,
      #sales-purchases-chart {
        width: 100% !important;
        height: 200px !important;
      }
    }
  }

  .table-container {
    margin: 20px 0 0 0 !important;
    padding: 10px !important;
    overflow-x: auto;

    .headerTable {
      flex-direction: column;
      height: auto;
      gap: 15px;

      .icon_text,
      .search_add {
        width: 100% !important;
        justify-content: center !important;
        flex-direction: column;
      }

      .search {
        input {
        }
      }

      .search-button {
        width: 100% !important;
        margin-top: 10px;
      }
    }

    .table-responsive {
      overflow-x: auto;

      table {
        min-width: 600px;

        th,
        td {
          padding: 8px 4px;
          font-size: 12px;
        }
      }
    }
  }
}
.switch {
  position: relative;
  display: inline-block;
  width: 24px;
  height: 14px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider - the moving part */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 10px;
  width: 10px;
  left: 4px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #3fad4c;
}

input:focus + .slider {
  box-shadow: 0 0 1px #3fad4c;
}

input:checked + .slider:before {
  transform: translateX(10px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
.delete-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.delete-popup-content {
  background: #ffffff;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.popup-actions {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.popup-actions button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.popup-actions button:first-child {
  background-color: #ff0000;
  color: #ffffff;
}

.popup-actions button:last-child {
  background-color: #cccccc;
  color: #000000;
}
</style>
