<template>
  <div
    :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'"
    style="
      display: flex;
      justify-content: space-between;
      align-items: center;
    "
  >
    <div class="search">
      <input type="text" :placeholder="$t('search')" />
      <SvgIcon class="SvgIcon" name="search"></SvgIcon>
    </div>
    <div
      class="haif_header"
      style="display: flex; justify-content: end; gap: 30px"
    >
      <div class="notification-container">
        <div class="icon-wrapper">
          <span class="bell-icon">
            <SvgIcon name="bell-icon"></SvgIcon>
          </span>
          <span class="notification-dot"></span>
        </div>
      </div>
      <div class="language-toggle">
        <button @click="toggleLanguage" :class="{ active: true }">
          <font-awesome-icon :icon="['fas', 'globe']" />
          <!--<span>{{ $i18n.locale === 'ar' ? '' : 'English' }}</span>-->
        </button>
      </div>
      <div class="user-profile">
        <button @click="toggleDropdown" class="profile-button">
          <img
            :src="user.image || require('@/assets/images/profileImage.jpg')"
            alt="User Image"
            class="profile-image"
          />
          <span class="user-name">{{ user.name }}</span>
          <span class="dropdown-icon">
            <SvgIcon name="dropdown-icon"></SvgIcon>
          </span>
        </button>
        <div v-if="isDropdownVisible" class="dropdown-menu">
          <ul>
            <router-link :to="{ name: 'profile' }">
              <li>{{ $t("profile") }}</li>
            </router-link>
            <router-link :to="{ name: 'forgetPassword' }">
              <li>{{ $t("forgetPasword") }}</li>
            </router-link>
            <li>
              <a href="javascript:void(0)" @click="handleClick" class="logout">
                {{ $t("logout") }}
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import SvgIcon from "@/components/SvgIcon.vue";
export default {
  data() {
    return {
      isDropdownVisible: false,
      user: {},
    };
  },
  components: {
    SvgIcon,
  },
  async created() {
    try {
      const token = localStorage.getItem("authToken");
      if (!token) {
        console.error("No token found. Redirecting to login.");
        if (this.$router) {
          this.$router.push({ name: "login" });
        } else {
          console.error("Router instance is not available.");
        }
        return;
      }
      const response = await axios.get("https://backend.alaqdk.com/api/user", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      this.user = response.data;
      this.$emit("user-data", this.user);
      if (!this.user || Object.keys(this.user).length === 0) {
        console.warn("User data is empty or undefined:", this.user);
        if (this.$router) {
          this.$router.push({ name: "login" });
        }
      }
    } catch (error) {
      console.error("Error fetching user data:", error);
      if (this.$toast) {
        this.$toast.error("Failed to fetch user data. Please log in again.");
      }
      if (this.$router) {
        this.$router.push({ name: "login" });
      }
    }
  },
  methods: {
    toggleDropdown() {
      this.isDropdownVisible = !this.isDropdownVisible;
    },
    handleClick() {
      localStorage.removeItem("authToken");
      this.$router.push("/login");
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang; // Update Vue I18n locale
      localStorage.setItem("lang", lang); // Save language preference
      document.documentElement.setAttribute(
        "dir",
        lang === "ar" ? "rtl" : "ltr"
      ); // Change direction for RTL
    },
    toggleLanguage() {
      const newLang = this.$i18n.locale === "ar" ? "en" : "ar";
      this.changeLanguage(newLang);
    },
  },
};
</script>

<style lang="scss" scoped>
.search {
  display: flex;
  align-items: center;
  position: relative;
  input {
    width: 390px;
    height: 44px;
    border-radius: 24px;
    padding-right: 40px;
    padding-left: 10px;
    gap: 4px;
    border: none;
    outline: none;
  }
  .SvgIcon {
    position: absolute;
    right: 15px;
  }
}
.notification-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  border-radius: 40px;
  margin: 0 5px 0 5px;
}

.icon-wrapper {
  position: relative;
  text-align: center;
  width: 24px; /* Adjust size */
  height: 25px;
  background-color: white; /* White background */
  border-radius: 50%; /* Black border */
  padding: 5px;
}

.bell-icon {
  width: 18px;
  height: 18pc; /* Adjust size */
}

.notification-dot {
  position: absolute;
  top: 6px; /* Adjust position */
  left: 20px;
  width: 6px; /* Adjust size */
  height: 6px;
  background-color: #ed3f36;
  border-radius: 50%;
}
.user-profile {
  position: relative;
}
.language-toggle {
  button {
    display: flex;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
    padding: 10px;
    border-radius: 50%;
    transition: background-color 0.3s;
    margin: 0 5px 0 5px;
    &.active {
      background-color: #ffffff;
      color: black;
      font-size: 20px;
    }

    span {
      font-size: 25px;
      font-weight: bold;
    }
  }
}
.profile-button {
  width: 161px;
  height: 44px;
  border-radius: 20px;
  padding: 6px 6px 6px 8px;
  display: flex;
  margin-left: 15px;
  justify-content: center;
  align-items: center;
  background-color: var(--primary-color); /*   */
  color: white;
  border: none;
  cursor: pointer;
}

.profile-image {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-left: 8px;
}

.user-name {
  width: 76px;
  height: 18px;
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
}

.dropdown-icon {
  margin-left: auto;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  [dir="rtl"] & {
    left: 20px;
  }
  [dir="ltr"] & {
    right: 10px;
  }
  background-color: #ffffff;
  border-radius: 5px;
}

.dropdown-menu ul {
  list-style-type: none;
  margin: 0 !important;
  padding: 5px 10px;
  width: 140px;
}

.dropdown-menu li {
  padding: 10px 5px;
  text-align: start;
}
a {
  border-radius: 12px;
  text-decoration: none !important;
  text-align: right;
  color: black;
}
.dropdown-menu li:hover a {
  background-color: var(--primary-color);
  color: white;
}
.dropdown-menu li:hover {
  background-color: var(--primary-color);
  color: #ffffff;
  cursor: pointer;
}
@media (min-width: 992px) and (max-width: 1199px) {
  .search input {
    width: 150px !important;
  }
  .haif_header {
    gap: 0px !important;
  }
  .profile-button {
    width: 100px;
  }
  select {
    width: 100px;
  }
  .notification-container {
    margin-right: 0px !important;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .search input {
    width: 150px !important;
  }
  .haif_header {
    gap: 0px !important;
  }
  select {
    width: 100px;
  }
  .profile-button {
    width: 100px;
  }
}
@media (max-width: 768px) {
  .search input {
    width: 100px !important;
  }
  .haif_header {
    gap: 0px !important;
  }
  .profile-button {
    width: 100px !important;
  }
  select {
    width: 90px;
  }
  .summary-container {
    width: 300px;
  }
}
</style>
